<template>
  <!-- Button trigger modal -->

  <!-- Modal -->
  <div
    class="modal fade mborder"
    id="shareModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="shareModalTitle"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="shareModalTitle">{{ share_modal_title }}</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="shareByLinkDiv my-block mborder">
            <span class="mb-2">{{share_link_str}}</span>
            <SwitchElement :checked="shareOn" @updated="updateShareLinkStatus" class="mt-2 mx-2"></SwitchElement>
            <button :disabled="!shareOn" @click="copyLinkToClipboard" class="btn btn-sm btn-link float-right">{{copy_str}}</button>
            <input :value="myWebClient+'share?ref='+shareLink" :disabled="!shareOn" type="text" ref="shareLinkOutput" class="form-control mborder mt-2" />
          </div>
          <div class="shareForPeople my-block mborder group-input">
            <span class="mb-2">{{share_with_str}}</span>
            <div class="row">
              <div class="col-auto no-padding mt-2" v-for="(email, i) in emails" v-bind:key="i">
                <span class="span-block mborder">
                  {{ email }}
                  <icon icon="user-minus" class="icon-btn mt-1" @click="remove_email(i)"></icon>
                </span>
              </div>
            </div>

            <input
              type="email"
              v-bind:style="(!isvalid())?'color:red':''"
              v-bind:class="'form-control mborder mt-2 '"
              placeholder="Nhập email"
              @keyup="add_email"
              v-model="current_input"
            />
            <button
              v-bind:disabled="!isvalid()&& !emails.length"
              @click="update_list_email"
              type="button"
              class="btn btn-sm float-right mt-3 btn-primary mborder"
            >{{ share_btn_str }}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SwitchElement from "./../../shared/SwitchElement";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faTimesCircle, faUserMinus } from "@fortawesome/free-solid-svg-icons";
import {myWebClient} from "../../../repositories/Repository" 
import { translate } from '../../shared/translator';
library.add(faTimesCircle, faUserMinus);
export default {
  data() {
    return {
      myWebClient:myWebClient,
      current_input: "",
      emails: [],
      share_modal_title:translate('share_modal_title'),
      share_btn_str:translate('Share'),
      share_with_str:translate('share_with'),
      copy_str:translate('Copy'),
      share_link_str:translate('share_link')
    };
  },
  components: {
    SwitchElement
  },
  methods: {
    copyLinkToClipboard(){
      this.$refs["shareLinkOutput"].select();
      this.$refs["shareLinkOutput"].setSelectionRange(0, 99999);
      document.execCommand("copy");

    },
    remove_email(index) {
      this.emails.splice(index, 1);
    },
    add_email(e) {
      if (e.code == "Enter") {
        let email = this.current_input;
        if (validateEmail(email)) {
          this.emails.push(email);
          this.current_input = "";
        }
      }
    },
    update_list_email() {
      try {
        if (this.current_input != "") {
          let email = this.current_input;
          if (validateEmail(email)) {
            this.emails.push(email);
            this.current_input = "";
          }
        }
        if(this.emails.length) this.$emit('commit_share', {emails: this.emails});
      } catch (error) {
        //console.log(error);
      }
    },
    isvalid(){
      return validateEmail(this.current_input);
    },
    updateShareLinkStatus(isOn){
      this.$emit('updateShareLinkStatus',{status:isOn})
    }
  },
  props:{
    lang:{
      type:String,
      default:"en"
    },
    shareLink:String,
    shareOn:Boolean
  }
};
function validateEmail(email) {
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}
</script>

<style>
.shareByLinkDiv {
  background-color: rgba(0, 0, 0, 0.055);
}
.shareForPeople {
  border: solid 1px whitesmoke;
}
.span-block {
  margin: 5px;
  margin-top: 20px;
  background-color: rgba(0, 0, 0, 0.089);
  padding: 5px;
  font-size: 15px;
}
.icon-btn {
  cursor: pointer;
  padding: 2px;
}
.icon-btn:hover {
  border-radius: 10px;
  background-color: white;
}
</style>
