<template>
  <div class="grid_container">
    <div class="grid_item" v-for="(topic,i) in topics" v-bind:key="i">
      <TopicWithBackground :choosed="choosed===topic.topicID"  :topic="topic" :ref="'topic'+i" @changed="updateTopic"></TopicWithBackground>
    </div>
  </div>
</template>
<script>
import TopicWithBackground from "../../shared/TopicWithBackground";
import { EventBus, SharedStorage } from "../../shared/event-bus";

export default {
  name: "topic-chooser",
  beforeMount() {
    // EventBus.$on("topic_changed", topic => {
    //   this.topics = topic;
    // });
    // this.topics=SharedStorage.topics;
    //console.log(this.choosed);
    
    // for(let i=0; i<this.topics.length; i++){
    //   if(this.topics[i].topicID==this.choosed){
    //     this.$refs["topic" + i][0].setChecked(true);
    //   }
    // }
  },
  components: {
    TopicWithBackground
  },
  props: {
    choosed:{
      type:Number| String,
      default:""
    },
    lang: {
      type: String,
      default: "en"
    }
  },
  data() {
    return {
      topics: SharedStorage.settings.topicSettings.topics
    };
  },
  methods: {
    updateTopic(topic) {
      //console.log("topic chooser triggled");
      
      if (topic.checked) {
        for (let i = 0; i < this.topics.length; ++i) {
          if (this.topics[i].topicID == topic.topicID) {
            // //console.log(this.$refs["topic" + i]);
            this.$refs["topic" + i][0].setChecked(true);
            // //console.log("true");
          } else {
            this.$refs["topic" + i][0].setChecked(false);
            // //console.log("false");
          }
        }
        this.$emit("topic_changed", topic);
        //console.log("topic change", topic);
        
      } else {
        this.$emit("delete_topic",topic);
      }
    }
  },
  chooseTopic(index) {
    //console.log(index);
  }
};
</script>

<style>
.grid_container {
  display: grid;
  grid-template-columns: auto auto;
  /* padding-right: 20px; */
  padding-top: 10px;
  padding-bottom: 20px;
  height: 300pt;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  height: 10px;
}
.grid_item {
  padding-left: 20px;
}
</style>