<template>
  <div>
    <TaskMenu v-if="currentView == 'task'" />
    <Calendar v-else />
  </div>
</template>

<script>
import Calendar from "./Calendar";
import TaskMenu from "./TaskMenu";
import { EventBus } from "../shared/event-bus";
export default {
  components: {
    Calendar,
    TaskMenu
  },
  beforeCreate() {
    EventBus.$on("viewChanged", view => {
      switch (view) {
        case "task":
          this.currentView = "task";
          break;

        default:
          this.currentView = "post";
          break;
      }
    });
  },
  data() {
    return {
      currentView: "post"
    };
  }
};
</script>

