import Repository from "./Repository"
import { setCookie, deleteAllCookies, getCookie } from "./cookieRepository"
import { baseDomain } from "./Repository"
import 'babel-polyfill'
const resource = "/users";
export default {
    loginWithGoogle: async function (access_token) {
        try {
            let result = await Repository.post(`${resource}/login`, { token: access_token });
            //console.log(result.data);
            setCookie("jhash", result.data.token);
            setCookie("_id", result.data._id);
            // return Promise.resolve(result.data);
            let profile = result.data;
            if (!profile.avatar.includes("http"))
                profile.avatar = `${baseDomain}/img/${profile.avatar}`;
            return Promise.resolve(profile);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    getProfile: async function (userID) {
        try {
            let result = await Repository.get(`${resource}/profiles`, {headers: {"Authorization":getCookie('jhash')}, params: { userID: userID } });
            // //console.log(result.data);
            let profile = result.data;
            if (!profile.avatar.includes("http"))
                profile.avatar = `${baseDomain}/img/${profile.avatar}`;
            return Promise.resolve(profile);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    logout() {
        //console.log("deleting cookie");
        deleteAllCookies();
        //console.log("deleted cookie")
    },
    updateProfile: async function (newProfile, avatar) {
        try {
            let data = new FormData();
            data.append('textContent', JSON.stringify(newProfile));
            if (avatar != null) data.append('avatar', avatar);
            let result = await Repository.put(`${resource}/profiles`, data,{headers: {"Authorization":getCookie('jhash')}});
            return Promise.resolve(result.data);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    logoutDevice: async function (ip) {
        try {
            if (ip != null) {
                let result = await Repository.delete(`${resource}/devices`, {headers: {"Authorization":getCookie('jhash')}, params: { ip: ip } });
                if (result.status == 204) {
                    return Promise.resolve();
                } else return Promise.reject(new Error(result.status))
            } else return Promise.reject("Unknow device ip")
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    getUserSettings: async function () {
        try {
            let result = await Repository.get(`${resource}/settings`,{headers: {"Authorization":getCookie('jhash')},});
            if (result.status == 200) {
                // //console.log(result.data);
                let settings = result.data;
                return Promise.resolve(settings);
            } else return Promise.reject(result.status)
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    updateUserSettings: async function (settings) {
        try {
            let result = await Repository.put(`${resource}/settings`, { settings: settings },{headers: {"Authorization":getCookie('jhash')},});
            if (result.status == 205) {
                //console.log("update thành công")
                return Promise.resolve();
            } else return Promise.reject(result.status);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    }
}