<template>
  <v-col v-if="task!=null" cols="3">
    <div>
      <div v-if="isLoading" class="extention-loading">
        <general-loading class="float-right mr-5" :color="'#c0f0fc'"></general-loading>
      </div>
      <div class="row justify-content-between">
        <div class="col-auto mt-3">
          <TickCheckbox>
            <input type="checkbox" v-model="task.done" />
          </TickCheckbox>
        </div>
        <div class="col no-padding">
          <p v-if="task.done" class="taskTitleDone mt-5">
            <del>{{task.taskContent}}</del>
          </p>
          <v-textarea
            v-else
            v-model="taskContent"
            :placeholder="translate('create_new_task')"
            :auto-grow="true"
            @keydown="checkEnterKey"
            @blur="update"
            :rows="1"
            counter="100"
            maxlength="100"
          ></v-textarea>
        </div>
        <div class="col-auto mt-3">
          <StarCheckbox>
            <input type="checkbox" v-model="task.star" />
          </StarCheckbox>
        </div>
      </div>
      <hr class="mr-3" />
      <div class="d-flex align-content-center justify-space-between">
        <v-btn icon class="mx-2" @click="task=null">
          <v-icon>mdi-application-import</v-icon>
        </v-btn>
        <DeleteTaskDialog v-if="task._id != undefined" @onDelete="deleteTask" />
      </div>
      <v-list class="mborder mr-3" rounded dense flat two-line>
        <!-- <v-list-item-group> -->
        <RemindOption v-model="task.remindTime" />
        <TagOption v-model="task.topic" />
        <DeadlineOption v-model="task.deadline" />
        <RepeatOption v-model="task.repeatSetting" />
        <!-- </v-list-item-group> -->
      </v-list>
    </div>
  </v-col>
</template>

<script>
import { EventBus } from "../../shared/event-bus";
import StarCheckbox from "./StarCheckbox";
import TickCheckbox from "./TickCheckbox";
import RemindOption from "./RemindOption";
import TagOption from "./TagOption";
import GeneralLoading from "../../shared/GeneralLoading";
import RepeatOption from "./RepeatOption";
import DeadlineOption from "./DeadlineOption";
import { translate } from "../../shared/translator";
import taskRepository from "../../../repositories/taskRepository";
import DeleteTaskDialog from "./DeleteTaskDialog";
export default {
  created() {
    EventBus.$on("showTaskEditor", task => {
      //console.log("Task Editor: ", task);
      this.task = task;
      this.taskContent=this.task.taskContent;
    });
  },
  methods: {
    translate: translate,
    async checkEnterKey(e) {
      if (event.key == "Enter" && this.taskContent.trim() != "") {
        this.update();
      }
    },
    update(){
      this.task.taskContent=this.taskContent;
    },
    async deleteTask() {
      try {
        await taskRepository.deleteTask(this.task._id);
      } catch (error) {
      } finally {
        EventBus.$emit("onTaskDeleted", this.task);
        this.task = null;
      }
    }
  },
  components: {
    GeneralLoading,
    StarCheckbox,
    TickCheckbox,
    RemindOption,
    TagOption,
    RepeatOption,
    DeadlineOption,
    DeleteTaskDialog
  },
  data() {
    return {
      isLoading: false,
      task: null,
      taskContent:null,
    };
  }
};
</script>

<style>
.taskContentInput {
  -ms-flex-wrap: wrap;
}
.task-title-editor {
  border: none;
}
.taskTitleDone {
  word-break: break-all;
  font-size: 20;
  max-width: 20rem;
}
v-text-field {
  padding-top: 0;
  margin-top: 0;
}
</style>