<template>
  <div class="my-block">
    <ul class="list-group">
      <li v-for="(topic,i) in topicTemp" v-bind:key="i" class="list-group-item topicsetting">
        <div class="input-group input-group-info">
          <input
            type="text"
            class="form-control input-info"
            disabled
            v-bind:value="translate(topic.title)"
            v-bind:id="'topic' + i"
            :ref="'topic_item_'+i"
            @change="update_topic(i)"
          />
        <span class="topic-tag float-right input-group-append" v-if="!topic.topicID.length">{{translate('Default')}}</span>
          <button
            v-if="topic.topicID.length"
            class="btn input-group-append btn-edit"
            @click="enable_input(i)"
          >
            <icon icon="edit" size="sm" />
          </button>
          <button
            v-if="topic.topicID.length"
            class="btn input-group-append btn-edit"
            @click="delete_topic(i)"
          >
            <icon icon="trash-alt" size="sm" />
          </button>
        </div>
      </li>
    </ul>
    <div class="row">
      <div class="col"></div>
      <div class="col">
        <button class="btn-primary mborder btn-add-topic float-right" @click="create_new_topic">
          <icon icon="plus" size="md" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { translate } from "../../shared/translator.js";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import crypto from "crypto";
library.add(faPlus);
export default {
  updated(){
    //console.log("Topic setting updated");
  },
  watch:{
    topicSettings: function(val){
      this.topicTemp= val
    }
  },
  data() {
    return {
      topicTemp: this.topicSettings
    };
  },
  props: {
    topicSettings: {
      type: Array,
      default: []
    },
    lang: {
      type: String,
      default: "en"
    }
  },
  methods: {
    translate: translate,
    enable_input: function(index) {
      //console.log(index);
      var input = document.getElementById("topic" + index);
      input.disabled = false;
      input.select();
    },
    update_topic(index) {
      this.topicTemp[index].title = this.$refs[`topic_item_${index}`][0].value;
      this.$emit("update", { topics: this.topicTemp });
    },
    delete_topic(index) {
      //TODO hỏi lại trước khi xóa bỏ topic
      this.topicTemp.splice(index, 1);
      this.$emit("update", { topics: this.topicTemp });
    },
    create_new_topic() {
      this.topicTemp.push({
        topicID: crypto.randomBytes(10).toString("hex"),
        title:translate("new_topic", this.lang)
      });
      //console.log("new topics", this.topicTemp);
    }
  }
};
</script>

<style>
.list-group-item, .topicsetting {
  padding: 0.55rem 0.75rem;
}
.btn-add-topic {
  width: 42px;
  height: 42px;
  outline: none !important;
  margin-right: 20px !important;
}
.delete-icons {
  width: 10%;
  height: 10%;
  cursor: pointer !important;
}
.input-info {
  background-color: white !important;
  border-radius: 32px !important;
  border: none;
}
.input-group-info {
  background-color: white;
  /* margin:20px; */
  margin-top: 5px;
  margin-right: 10px !important;

  /* padding: 5px; */
  border-radius: 10px;
}
.btn-edit {
  border-radius: 32px !important;
}
.topic-tag{
  font-size: 15px;
  color:lightgreen
}
</style>