<template>
  <v-toolbar flat color="black">
    <v-row>
      <v-col class="d-flex justify-content-between" cols="5">
        <a class="align-self-center d-flex" href="#" @click="refreshMyApp">
          <img class="logo align-self-center" src="./logo.png" />
          <span class="brand align-self-center d-none d-md-inline">Memo</span>
        </a>
        <v-btn-toggle class="align-self-center" group dense>
          <v-btn @click="change_active_view('post')">
            <v-icon v-text="'mdi-notebook'" color="white"></v-icon>
            <p v-if="activeView=='post'" class="tab-name d-none d-md-flex">{{translate('Note')}}</p>
          </v-btn>
          <v-btn @click="change_active_view('task')">
            <v-icon v-text="'mdi-calendar-check'" color="white"></v-icon>
            <p v-if="activeView=='task'" class="tab-name d-none d-md-flex">{{translate('Task')}}</p>
          </v-btn>
        </v-btn-toggle>
        <!-- <v-tabs class="align-self-center" dark background-color="black" align-with-title :show-arrows="false" right >
          <v-tab>
          </v-tab>
          <v-tab>
          </v-tab>
        </v-tabs>-->
      </v-col>
      <v-col class="d-flex" cols="4">
        <SearchBar class="align-self-center" @search="searchPost" />
      </v-col>
      <v-col class="d-flex justify-content-between" cols="3">
        <div class="d-flex" @click="change_active_tab('profile')">
          <Avatar
            class="avatar align-self-center"
            :src="user.avatar"
            :username="user.name"
            :size="40"
          />
          <span
            :class="{'username d-sm-none d-md-inline mx-2 my-2 align-self-center':true,'nav-active-btn':(activeExtension=='profile')}"
          >{{ user.name }}</span>
        </div>
        <div class="align-self-center">
          <icon
            v-if="activeView=='post'"
            @click="change_active_tab('filter')"
            icon="filter"
            size="lg"
            :class="{'icons float-right mx-2 align-self-center':true, 'nav-active-btn':(activeExtension =='filter')}"
          />
          <icon
            @click="change_active_tab('settings')"
            icon="cog"
            size="lg"
            :class="{'icons float-right mx-2 align-self-center':true, 'nav-active-btn':(activeExtension == 'settings')}"
          />
          <!-- <icon
            v-if="activeView=='post'"
            @click="change_active_tab('filter')"
            icon="bell"
            size="lg"
            :class="{'icons float-right mx-2 align-self-center':true}"
          />-->
        </div>
      </v-col>
    </v-row>
  </v-toolbar>
</template>

<script>
import SearchBar from "./SeachBar";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faFilter,
  faCog,
  faLanguage,
  faHome,
  faList
} from "@fortawesome/free-solid-svg-icons";
import { EventBus, SharedStorage } from "../shared/event-bus";
import Avatar from "vue-avatar";
import { translate } from "../shared/translator";
library.add(faFilter, faCog, faLanguage, faHome, faList);
export default {
  beforeCreate() {
    EventBus.$on("showTaskEditor", task => {
      //console.log("show task event receive in navbar");
      this.activeExtension = "";
    });
  },
  beforeMount() {
    this.user = SharedStorage.currentUser;
  },
  data() {
    return {
      activeExtension: "filter",
      activeView: "post",
      user: {}
    };
  },
  components: {
    SearchBar,
    Avatar
  },
  methods: {
    translate: translate,
    change_active_tab: function(tab_name) {
      EventBus.$emit("extensionChanged", tab_name);
      this.activeExtension = tab_name;
    },
    change_active_view: function(view_name) {
      EventBus.$emit("viewChanged", view_name);
      this.activeView = view_name;
    },
    searchPost(e) {
      this.$emit("search", e);
    },
    refreshMyApp(e) {
      EventBus.$emit("refresh_app", e);
    }
  }
};
</script>

<style>
.my-nav {
  background-color: black;
}
.avatar {
  cursor: pointer;
}
.username {
  color: white;
  cursor: pointer;
}
.icons {
  color: white;
  /* width: 40px; */
  /* height: 40px; */
  /* margin: 10px; */
  /* padding: 2px; */
  cursor: pointer;
  /* border: solid 1px; */
  /* border-radius: 10px; */
}
.no-padding {
  padding: 0 0 !important;
}
.logo {
  width: 40px;
  height: 40px;
  margin: auto;
}
.brand {
  font-size: 30px;
  color: whitesmoke;
  font: Segoe UI;
}
.nav-btn {
  background-color: transparent;
  border: none;
  border-radius: 32px !important;
}
.nav-active-btn {
  color: #007bff;
}
.link-navbar:hover {
  text-underline-position: unset;
}
.tab-name {
  margin-top: 0.2rem;
  font-size: 0.6rem;
  color: white;
}
.tab {
  padding: 0 !important;
}
</style>
