import Repository from "./Repository"
import 'babel-polyfill'
import { baseDomain } from "./Repository"
import { getCookie } from "./cookieRepository";
const resource = "/posts";
export default {
    async getListPost(start, filter = null) {
        try {
            //console.log("filter",filter);
            let params = { start: start,limit:10, includeFreezedPost: false };
            if (filter != null) {
                if (filter.includeFreezedPost === true) {
                    params.includeFreezedPost = true;
                }
                if (filter.contents != undefined && filter.contents.length) {
                    //console.log(filter.contents)
                    for (let i = 0; i < filter.contents.length; i++) {
                        // //console.log(filter.contents[i])
                        params[filter.contents[i]] = true;
                    }
                }
                if (filter.feelings != undefined && filter.feelings.length) {
                    // for (let i = 0; i < filter.feelings.length; i++) {
                    params['feeling'] = JSON.stringify(filter.feelings)
                    // //console.log(filter.feelings)
                    // }
                }
                if (filter.topics != undefined && filter.topics.length) {
                    // for (let i = 0; i < filter.topics.length; i++) {
                    params['topic'] = JSON.stringify(filter.topics)
                    // //console.log(filter.feelings)
                    // }
                }
                if (filter.title != undefined && filter.title != null) params['titleInclude'] = filter.title;
                if (filter.time != undefined && filter.time != null) {
                    params['fromTime'] = (new Date(filter.time.from)).getTime();
                    params['toTime'] = (new Date(filter.time.to)).getTime();
                }
            }
            //console.log("param", params);
            let result = await Repository.get(`${resource}`, {headers: {"Authorization":getCookie('jhash')}, params: params });
            //console.log(result);
            let posts = result.data.posts;
            //TODO comment dòng này vì thay đổi cách thức load post về
            posts=addServerDomain(posts);
            //console.log(posts)
            // return Promise.resolve({posts:posts,totalCount:result.data.totalCount});
            return Promise.resolve({ posts: posts });

        } catch (e) {
            //console.log("get post",e);
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async getSharePost(token) {
        try {
            let post = await Repository.get(`${resource}/copies`, {headers: {"Authorization":getCookie('jhash')}, params: { ref: token } })
            //console.log(post);
            let sharepost = addServerDomain([post.data]);
            return Promise.resolve(sharepost[0]);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async getPost(postID) {
        try {
            let result = await Repository.get(`${resource}/post`, {headers: {"Authorization":getCookie('jhash')}, params: { postID: postID } })
            let posts = addServerDomain([result.data]);
            return Promise.resolve(posts[0]);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async uploadPost(textContents, mediaContents) {
        let data = new FormData();
        data.append('textContents', JSON.stringify(textContents));
        for (let i = 0; i < mediaContents.images.length; i++) {
            data.append('images', mediaContents.images[i]);
        }
        for (let i = 0; i < mediaContents.videos.length; i++) {
            data.append('videos', mediaContents.videos[i]);
        }
        for (let i = 0; i < mediaContents.audios.length; i++) {
            data.append('audios', mediaContents.audios[i].blob);
        }
        //console.log("form ", data)
        try {
            let result = await Repository.post(`${resource}`, data,{headers: {"Authorization":getCookie('jhash')},});
            //console.log(result.status);
            return Promise.resolve(result.data);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }

    },
    async deletePost(postID) {
        try {
            let result = await Repository.delete(`${resource}`, {headers: {"Authorization":getCookie('jhash')}, params: { postID: postID } });
            //console.log(result.status);
            return Promise.resolve();
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async updatePost(postID, updatedTextContents, updatedMediaContents = null) {
        let updatedData = new FormData();
        let newTextContents = removeServerDomain(updatedTextContents);
        updatedData.append('textContents', JSON.stringify(newTextContents));
        if (updatedMediaContents != null) {
            for (let i = 0; i < updatedMediaContents.images.length; i++) {
                updatedData.append('images', updatedMediaContents.images[i]);
            }
            for (let i = 0; i < updatedMediaContents.videos.length; i++) {
                updatedData.append('videos', updatedMediaContents.videos[i]);
            }
            for (let i = 0; i < updatedMediaContents.audios.length; i++) {
                updatedData.append('audios', updatedMediaContents.audios[i].blob);
            }
        }
        try {
            let result = await Repository.put(`${resource}`, updatedData, {headers: {"Authorization":getCookie('jhash')}, params: { postID: postID } });
            //console.log(result.status);
            return Promise.resolve();
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async freezePost(postID, isFreeze = true) {
        try {
            let result = await Repository.put(`${resource}/freeze_post`, {}, {headers: {"Authorization":getCookie('jhash')}, params: { postID: postID, isFreeze: isFreeze } });
            //console.log(result.status);
            return Promise.resolve();
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async share_post_to_email(postID, listEmail) {
        try {
            let data = { postID: postID, listEmail: listEmail };
            let result = await Repository.post(`${resource}/emails`, data,{headers: {"Authorization":getCookie('jhash')},});
            //console.log(result);
            return Promise.resolve(result.data);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async updatePostShareLinkStatus(postID, status) {
        try {
            let result = await Repository.put(`${resource}/copies`, { shareLinkStatus: status }, {headers: {"Authorization":getCookie('jhash')}, params: { postID: postID } });
            //console.log(result.status);
            return Promise.resolve();
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    }
}
function addServerDomain(posts) {
    for (let pi = 0; pi < posts.length; pi++) {
        if (posts[pi].user != undefined && posts[pi].user.avatar != undefined && !posts[pi].user.avatar.includes("http")) {
            posts[pi].user.avatar = `${baseDomain}/api/file/${posts[pi].user.avatar}`
        }
        for (let ii = 0; ii < posts[pi].images.length; ii++) {
            //console.log(posts[pi].images[ii])
            posts[pi].images[ii] = baseDomain + "/api/file/" + posts[pi].images[ii];
            //console.log(posts[pi].images[ii])
        }
        for (let vi = 0; vi < posts[pi].videos.length; vi++) {
            posts[pi].videos[vi] = baseDomain + "/api/file/" + posts[pi].videos[vi];
        }
        for (let ai = 0; ai < posts[pi].audios.length; ai++) {
            posts[pi].audios[ai] = baseDomain + "/api/file/" + posts[pi].audios[ai];
        }
        //console.log(pi)
    }
    return posts;
}
function removeServerDomain(textContents) {
    for (let ii = 0; ii < textContents.images.length; ii++) {
        textContents.images[ii] = textContents.images[ii].substr(baseDomain.length + 5);
        //5 là độ dài của '/img/'
    }
    for (let vi = 0; vi < textContents.videos.length; vi++) {
        textContents.videos[vi] = textContents.videos[vi].substr(baseDomain.length + 5);
        //5 là độ dài của '/vid/'
    }
    for (let ai = 0; ai < textContents.audios.length; ai++) {
        textContents.audios[ai] = textContents.audios[ai].substr(baseDomain.length + 5);
        //5 là độ dài của '/aud/'
    }
    return textContents;
}