<template>
  <div class="accordion" id="timeline">
    <h4>{{taskString}}</h4>
    <hr />
    <v-list shaped class="mborder">
      <v-list-item-group color="primary">
        <v-list-item v-for="(item, i) in listView" :key="i" @click="changeTaskView(item.name)">
          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="viewString(item.name)"></v-list-item-title>
          </v-list-item-content>
          <v-badge v-if="item.count>0" class="mr-4" color="blue" :content="item.count"></v-badge>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<script>
import { translate } from "../shared/translator";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faSun,
  faStar,
  faCalendarAlt,
  faCalendarTimes
} from "@fortawesome/free-solid-svg-icons";
import { EventBus } from "../shared/event-bus";
library.add(faSun, faStar, faCalendarAlt, faCalendarTimes);
export default {
  beforeCreate() {
    EventBus.$on("taskCountChanged", counter => {
      this.listView[0].count= counter.today;
      this.listView[1].count= counter.passed;
      this.listView[2].count= counter.important;
      this.listView[3].count= counter.future;
      this.listView[4].count= counter.completed;
    });
  },
  methods: {
    changeTaskView(taskViewName) {
      this.currentTaskView = taskViewName;
      EventBus.$emit("taskViewChanged", taskViewName);
    },
    viewString: name => translate(name)
  },
  computed: {
    taskString() {
      return translate("Task");
    }
  },
  data() {
    return {
      currentTaskView: "today",
      listView: [
        { name: "today", icon: "mdi-calendar-today", count: 0 },
        { name: "task_passed", icon: "mdi-calendar-remove", count: 0 },
        { name: "important", icon: "mdi-calendar-star", count: 0 },
        { name: "future", icon: "mdi-calendar-search", count: 0 },
        { name: "completed", icon: "mdi-calendar-check", count: 0 }
      ]
    };
  }
};
</script>

<style>
.accordion {
  border-radius: 10px;
  background-color: whitesmoke;
  padding: 20px;
  /* margin-right: 20px; */
  /* width: 50%; */
  height: 90vh;
}
.noBottom {
  margin-bottom: 0;
}
.no-padding {
  padding: 0.75rem 0;
}
</style>