<template>
  <div class="input-group input-group-search">
    <input
      class="form-control transparent"
      type="search"
      v-bind:placeholder="placeholder"
      aria-label="Search"
      v-model="searchValue"
      @keyup="enterToSearch"
    />
    <button @click="search" class="btn input-group-append transparent">
      <icon
            icon="search"
            class="float-right ml-2 align-self-center"
          />
    </button>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { translate } from '../shared/translator';
import { SharedStorage, EventBus } from '../shared/event-bus';
library.add({faSearch})
export default {
  props:{
    lang:{
      type:String,
      default:"en"
    },
  },
  data() {
    return {
      placeholder:translate('search_placeholder'),
      searchValue:''
    };
  },
  methods:{
    enterToSearch(e){
      //console.log(e);
      if(e.code=="Enter") this.search();
    },
    search(){
      //TODO đảm bảo chống XSS
      SharedStorage.currentFilter.title=this.searchValue+'';
      EventBus.$emit('filter_changed');
    }
  }
};
</script>

<style>
.input-group-search{
  border-radius: 32px !important;
  background-color:white;
  padding: 2px;
}
.input-group-search:focus{
  border-radius: 32px !important;
  background-color:white;
  padding: 2px;
}
.transparent{
  border-radius: 10px;
  background-color:transparent;
  border:transparent !important;
  outline:none;
}
.transparentz:focus{
  background-color:transparent;
  border:transparent !important;
  outline:none;
}

</style>
