<template>
  <v-menu offset-y absolute>
    <template v-slot:activator="{ on }">
      <v-list-item v-on="on" :two-line="topic!=null">
        <v-list-item-avatar>
          <v-icon v-text="'mdi-tag'" :class="{'success white--text': (topic!=null)}"></v-icon>
        </v-list-item-avatar>
        <!-- <v-list-item-content v-if="topic !=null && topic.title!=null">{{translate(topic.title)}}</v-list-item-content> -->
        <v-list-item-content v-on="on">
          <v-list-item-title>{{translate("Topics")}}</v-list-item-title>
          <v-list-item-subtitle v-if="topic!=null">{{translate(topic.title)}}</v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action v-show="topic!=null">
          <v-btn icon @click="update(null)">
            <v-icon v-text="'mdi-close'"></v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </template>
    <v-list>
      <v-list-item v-for="topic in topics" :key="topic.topicID" @click="update(topic)">
        <v-list-item-avatar>
          <TopicWithBackground :topic="topic" :iconOnly="true" />
        </v-list-item-avatar>
        <v-list-item-content>{{translate(topic.title)}}</v-list-item-content>
      </v-list-item>
    </v-list>
  </v-menu>
</template>

<script>
import { translate } from "../../shared/translator";
import TopicChooser from "../../body/post_editor_components/TopicChooser";
import TopicWithBackground from "../../shared/TopicWithBackground";
import { SharedStorage, EventBus } from "../../shared/event-bus";
export default {
  created() {
    EventBus.$on("showTaskEditor", task => {
      this.topic = task.topic;
    });
  },
  methods: {
    translate: translate,
    update(e) {
      //console.log(e);
      this.topic = e;
      this.$emit("input", this.topic);
    }
  },
  props: {
    value: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      topic: this.value,
      topics: SharedStorage.settings.topicSettings.topics
    };
  },
  components: {
    TopicChooser,
    TopicWithBackground
  }
};
</script>

