export function translate(str) {
    let dic = vi;
    switch (SharedStorage.language) {
        case "vi":
            dic = vi;
            break;
        default:
            dic = en;
            break;
    }
    return (dic[`${str}`] != undefined) ? dic[`${str}`] : str;
}
const dayOfWeek = [
    new Date(2000, 0, 9, 1),//Sat
    new Date(2000, 0, 3, 1),//Sun
    new Date(2000, 0, 4, 1),//Mon
    new Date(2000, 0, 5, 1),//Tue
    new Date(2000, 0, 6, 1),//Wed
    new Date(2000, 0, 7, 1),//Thu
    new Date(2000, 0, 8, 1),//Fri
]
export class TimeConverter {
    constructor() {
        if (SharedStorage.language == "en") {
            //console.log("i am here");

            dateFormat.i18n = {
                dayNames: [
                    'CN', 'Th2', 'Th3', 'Th4', 'Th5', 'Th6', 'Th7',
                    'Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'
                ],
                monthNames: [
                    'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec',
                    'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'
                ],
                timeNames: [
                    'a', 'p', 'am', 'pm', 'A', 'P', 'AM', 'PM'
                ]
            };
        }
    }
    weekdayName(number) {
        return dateFormat(dayOfWeek[number], "ddd")
    }
    today(hour, minute) {
        let t = new Date();
        return (new Date(t.getFullYear(), t.getMonth(), t.getDate(), hour, minute)).getTime();
    }
    tomorow(hour, minute) {
        let t = new Date();
        t.setDate(t.getDate() + 1);
        return (new Date(t.getFullYear(), t.getMonth(), t.getDate(), hour, minute)).getTime();
    }
    nextweek(hour, minute) {
        let t = new Date();
        t.setDate(t.getDate() + 7);
        return (new Date(t.getFullYear(), t.getMonth(), t.getDate(), hour, minute)).getTime();
    }
    yesterday(hour, minute) {
        let t = new Date();
        t.setDate(t.getDate() - 1);
        return (new Date(t.getFullYear(), t.getMonth(), t.getDate(), hour, minute)).getTime();
    }
    convertTimeFromNow(str) {
        //console.log(str);
        let date = new Date(str);
        let today = new Date();
        let tomorow = (new TimeConverter()).tomorow(1, 0);
        tomorow = new Date(tomorow);
        let nextweek = (new TimeConverter()).nextweek(1, 0);
        nextweek = new Date(nextweek);
        let yesterday = (new TimeConverter()).yesterday(1, 0);
        yesterday = new Date(yesterday);
        let time = dateFormat(date, "(ddd) HH:MM");
        // let time = DateFormat("(EE) HH:mm").format(date);
        if (date.getFullYear() == nextweek.getFullYear() &&
            date.getMonth() == nextweek.getMonth() &&
            date.getDate() == nextweek.getDate()) {
            //console.log("next week");
            return translate("next_week") + " " + time;
        }
        if (date.getFullYear() == today.getFullYear() &&
            date.getMonth() == today.getMonth() &&
            date.getDate() == today.getDate()) {
            return translate("today") + " " + time;
        }
        if (date.getFullYear() == yesterday.getFullYear() &&
            date.getMonth() == yesterday.getMonth() &&
            date.getDate() == yesterday.getDate()) {
            return translate("yesterday") + " " + time;
        }
        if (date.getFullYear() == tomorow.getFullYear() &&
            date.getMonth() == tomorow.getMonth() &&
            date.getDate() == tomorow.getDate()) {
            return translate("tomorow") + " " + time;
        }
        return dateFormat(date, "dd/mm/yy") + " " + time;
    }
}