import Repository from "./Repository"
import 'babel-polyfill'
import { getCookie } from "./cookieRepository";
const resource = "/tasks";
export default {
    async getListTask(start) {
        try {
            let params = { start: start, limit: 50 };
            let result = await Repository.get(`${resource}`, { headers: { "Authorization": getCookie('jhash') }, params: params });
            let arr = standardizeTask(result.data);
            return Promise.resolve(arr);
        } catch (e) {
            //console.log("get task", e);
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async getTask(taskID) {
        try {
            let result = await Repository.get(`${resource}/task`, { headers: { "Authorization": getCookie('jhash') }, params: { taskID: taskID } })
            return Promise.resolve(result.data);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async uploadTask(taskData) {
        //console.log(taskData);
        try {
            let result = await Repository.post(`${resource}`, taskData, { headers: { "Authorization": getCookie('jhash') }, });
            //console.log(result.status);
            return Promise.resolve(result.data);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }

    },
    async deleteTask(taskID) {
        try {
            let result = await Repository.delete(`${resource}`, { headers: { "Authorization": getCookie('jhash') }, params: { taskID: taskID } });
            //console.log(result.status);
            return Promise.resolve(true);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
    async updateTask(taskID, updatedTask) {
        try {
            //console.log(updatedTask);
            let result = await Repository.put(`${resource}`, updatedTask, { headers: { "Authorization": getCookie('jhash') }, params: { taskID: taskID } });
            //console.log(updatedTask);
            //console.log(result);
            return Promise.resolve(result.data);
        } catch (e) {
            if (e.response != undefined && e.response.status != undefined) {
                return Promise.reject(e.response.status);
            } else return Promise.reject(0);
        }
    },
}
function standardizeTask(arr) {
    // //console.log(arr)
    // arr.forEach(element => {
    //     element = handlerNull(element);
    //     if (element.remindTime && element.remindTime != null)
    //         element.remindTime = new Date(element.remindTime);
    //     if (element.deadline && element.deadline != null)
    //         element.deadline = new Date(element.dateline);
    //     if (element.repeat && element.repeat != null) {

    //     }
    // });
    return arr;
}
function handlerNull(obj) {
    //console.log(obj);
    Object.keys(obj).forEach(function (index) {
        if (obj[index] == "")
            obj[index] = null;
        // else {
        //     obj[index] = handlerNull(obj[index]);
        // }
    });
    return obj;
}