<template>
  <tr>
    <td class="d-flex justify-content-start">
      <TickCheckbox class="align-self-center">
        <input v-model="taskData.done" type="checkbox" />
      </TickCheckbox>
    </td>
    <td>
      <div class="d-flex-collumn" @click="showTaskEditor">
        <p v-if="taskData.done">
          <del>{{taskData.taskContent}}</del>
        </p>
        <p v-else>{{taskData.taskContent}}</p>
        <div class="d-flex">
          <TopicInfo class="mr-3" :topic="taskData.topic" />
          <DeadlineInfo :deadline="task.deadline" />
          <v-icon
            v-text="'mdi-calendar-sync'"
            v-if="taskData.repeatSetting != null"
            small
            color="purple mx-2"
          />
          <v-icon
            v-text="'mdi-bell-check'"
            v-if="taskData.remindTime != null"
            small
            color="blue mx-2"
          />
        </div>
      </div>
    </td>
    <td class="d-flex justify-content-end">
      <StarCheckbox class="align-self-center">
        <input type="checkbox" v-model="taskData.star" />
      </StarCheckbox>
    </td>
  </tr>
</template>

<script>
import CheckboxElement from "../../shared/CheckboxElement";
import TickCheckbox from "../../right_extensions/task_editor_components/TickCheckbox";
import StarCheckbox from "../../right_extensions/task_editor_components/StarCheckbox";
import DeadlineInfo from "./DeadlineInfo";
import TopicInfo from "./TopicInfo";
import { EventBus } from "../../shared/event-bus";
import taskRepository from "../../../repositories/taskRepository";
export default {
  watch: {
    taskData: {
      deep: true,
      handler() {
        this.updateTask();
      }
    }
  },
  methods: {
    async updateTask() {
      try {
        let tasks = await taskRepository.updateTask(
          this.taskData._id,
          this.taskData
        );
        // tasks.forEach(element => {
        //   // check done 1 task repeat có thể tạo thêm 1 task nữa
        // });
          this.$emit("onTaskChanged", {tasks:tasks});
      } catch (error) {
        //console.log(error);
      }
    },
    showTaskEditor() {
      //console.log("click table row", this.taskData);
      EventBus.$emit("showTaskEditor", this.taskData);
      // EventBus.$emit("showTaskEditor", this.taskData);//Emit 2 lần để fix lỗi các component con của TaskEditor không load kịp
    }
  },
  components: {
    CheckboxElement,
    TopicInfo,
    TickCheckbox,
    StarCheckbox,
    DeadlineInfo
  },
  data() {
    return {
      taskData: this.task
    };
  },
  props: {
    task: {
      type: Object,
      default: undefined
    }
  }
};
</script>

<style>
.taskTitleText {
  font-size: 20px;
  margin-bottom: 0;
}
.taskOptionText {
  font-size: 10pt;
  margin-bottom: 0;
}
</style>