<template>
  <v-col cols="3">
    <div class="px-3 scroll_extensions">
      <div v-if="isLoading" class="extention-loading mborder">
        <general-loading class="float-right mr-5" :color="'#c0f0fc'"></general-loading>
      </div>
      <button
        v-else
        @click="applyFilter"
        class="btn btn-primary btn-function float-right"
      >{{ translate("Apply") }}</button>
      <h4 class="mt-1">{{translate("Filters")}}</h4>
      <hr class="mr-3" />
      <ContentFilter id="id_quote-left_element"></ContentFilter>
      <br>
      <FeelingFilter  class="collapse show" id="id_grin-beam_element"></FeelingFilter>
      <br>
      <TopicFilter
        :topics="userTopics"
        class="collapse show"
        id="id_air-freshener_element"
      ></TopicFilter>
    </div>
  </v-col>
</template>

<script>
import CollapseButton from "./../../shared/CollapseButton";
import ContentFilter from "./ContentFilter";
import FeelingFilter from "./FeelingFilter";
import GeneralLoading from "../../shared/GeneralLoading";
import TopicFilter from "./TopicFilter";
import { translate } from "../../shared/translator";
import { EventBus } from "../../shared/event-bus";
export default {
  props: {
    lang: {
      type: String,
      default: "en"
    },
    userTopics: Array
  },
  data() {
    return {
      haveContentArr: [],
      haveFeelingArr: [],
      haveTopicArr: [],
      isLoading: false
    };
  },
  components: {
    CollapseButton,
    TopicFilter,
    ContentFilter,
    FeelingFilter,
    GeneralLoading
  },
  methods: {
    translate: translate,
    applyFilter() {
      EventBus.$emit("filter_changed");
    }
  }
};
</script>

<style>
.scroll{
  overflow-y: scroll;
}
</style>
