<template>
    <!-- <v-card class="extensiontab" elevation="10"> -->
    <component  v-bind:is="component"></component>
    <!-- <Filters class="scroll_extensions" v-if="active_tab == 0" />
    <Profile class="scroll_extensions" v-if="active_tab == 1" />
    <Setting class="scroll_extensions" v-if="active_tab == 2" />
    <TaskEditor class="scroll_extensions" v-if="active_tab==3" />-->
    <!-- </v-card> -->
</template>

<script>
import Profile from "./Profile";
import Filters from "./filters/Filters";
import Setting from "./settings/Setting";
import { errorMessages } from "../shared/errorMessage";
import TaskEditor from "./task_editor_components/TaskEditor";
import { EventBus } from "../shared/event-bus";
export default {
  components: {
    Profile,
    Setting,
    Filters,
    TaskEditor
  },
  beforeCreate() {
    EventBus.$on("viewChanged", view => {
      this.component = null;
      switch (view) {
        case "task":
          this.active_tab = 3;
          this.component = TaskEditor;
          break;

        default:
          this.component = Filters;
          this.active_tab = 0;
          break;
      }
    });
    EventBus.$on("extensionChanged", e => {
      //console.log("event bus change extension", e);
      this.component = null;
      switch (e) {
        case "filter":
          this.active_tab = 0;
          this.component = Filters;
          break;
        case "settings":
          this.active_tab = 2;
          this.component = Setting;
          break;
        default:
          this.active_tab = 1;
          this.component = Profile;
          break;
      }
    });
  },
  data() {
    return {
      active_tab: 0,
      component:Setting
    };
  }
};
</script>

<style>
.extention-loading {
  position: absolute;
  width: inherit;
  height: inherit;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(
    255,
    255,
    255,
    0.3
  ); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: none; /* Add a pointer on hover */
  text-align: center;
  padding-top: 10pt;
}

.btn-function {
  border-radius: 10px;
  margin-right: 10px;
}
.btn-function:hover {
  border-radius: 10px;
  margin-right: 10px;
}
.btn-function:active {
  border-radius: 10px;
  margin-right: 10px;
}
</style>
