<template>
  <div>
    <div v-if="isLoading" class="post-loading">
      <div class="row" style="width:100%; height:100%">
        <div class="col-2">
          <general-loading :color="'#c0f0fc'"></general-loading>
        </div>
        <div class="col-10">
          <post-loading></post-loading>
        </div>
      </div>
    </div>
    <div v-else>
      <div v-if="editing && index>=0" class="edit-post">
        <NewPostEditor
          :editMode="true"
          :user="user"
          :post="post"
          @edit_canceled="cancel_edit"
          @post_updated="update_post"
        ></NewPostEditor>
      </div>
      <div v-else class="post">
        <MoreOptionDialog
          v-if="index>=0"
          class="float-right"
          :post_id="post.id"
          :freezed="(post.status == 'freezed')"
          @delete="$emit('delete_action', { id: post.id, index: index })"
          @share="$emit('share_action', { id: post.id, index: index })"
          @freeze="$emit('freeze_action', { id: post.id, index: index })"
          @edit="edit_post"
        ></MoreOptionDialog>
        <div class="show-post">
            <PostHeader :post="post" />
              <p v-show="post.title.length" class="col-auto post-content-text mt-3">{{ post.title }}</p>
          <div class="grid-container">
            <img
              v-for="(image, ii)  in post.images"
              v-bind:key="'image'+ii"
              :class="'image-show mborder '+((ii==0 || post.images.length <=2)?'single-image':((post.images.length >3)?'grid-image-tripple':'grid-image-double'))"
              :src="image"
              v-show="ii<4"
              @click="onClick(ii)"
            />
            <audio
              style="display:inline-block; outline:none !important; border:none; width:100%"
              v-for="(audio, vi)  in post.audios"
              v-bind:key="'audio'+vi"
              controls
            >
              <source :src="audio" type="audio/ogg" />
              <source src="horse.mp3" type="audio/mpeg" />Your browser does not support the audio element.
            </audio>
            <video
              v-for="(video, vi)  in post.videos"
              v-bind:key="'video'+vi"
              :class="'mborder '+((vi==0 || post.videos.length <=2)?'single-image':((post.videos.length >3)?'grid-image-tripple':'grid-image-double'))"
              controls
            >
              <source :src="video" />
              <source src="movie.ogg" type="video/ogg" />Your browser does not support the video tag.
            </video>
          </div>
          <vue-gallery-slideshow
            :images="post.images"
            :index="image_index"
            @close="image_index = null"
          ></vue-gallery-slideshow>
          <span></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import VueGallerySlideshow from "vue-gallery-slideshow";
import PostHeader from "./PostHeader";
import NewPostEditor from "../post_editor_components/NewPostEditor";
import PostLoading from "../../shared/PostLoading";
import GeneralLoading from "../../shared/GeneralLoading";
import { translate } from "../../shared/translator";

import {
  faClock,
  faMapMarkedAlt,
  faUserLock,
  faCalendarCheck
} from "@fortawesome/free-solid-svg-icons";

library.add(faClock, faMapMarkedAlt, faUserLock, faCalendarCheck);
import MoreOptionDialog from "./MoreOptionDialog";
import { EventBus, SharedStorage } from "../../shared/event-bus";
import postRepository from "../../../repositories/postRepository";
import { errorMessages } from "../../shared/errorMessage";
import Avatar from "vue-avatar";
export default {
  name: "post",
  async created() {
    this.user = SharedStorage.currentUser;
    EventBus.$on("edit_called", postID => {
      //console.log("edit được gọi từ event bus");

      if (this.post._id === postID) {
        this.temp_post = this.post;
        this.editing = true;
      }
    });
    if (this.index != -1) {
      //xem ở chế dộ bình thường
      let id = this.postID;
      try {
        this.isLoading = true;
        this.post = await postRepository.getPost(id);
        //console.log("post", this.post);
      } catch (error) {
        //console.log("loadpost bị lỗi", error);
        EventBus.$emit("showError", {
          message: errorMessages["load_post_error"]
        });
      } finally {
        this.isLoading = false;
      }
    } else {
      this.post = this.share_post;
    }
  },
  data() {
    return {
      post: {},
      user: {},
      temp_post: {},
      editing: false,
      image_index: null,
      security: "Bí mật",
      images: this.getImageScr,
      isLoading: false
    };
  },
  props: {
    postID: String,
    index: Number,
    share_post: {},
    lang: { type: String, default: "en" }
  },
  components: {
    MoreOptionDialog,
    VueGallerySlideshow,
    NewPostEditor,
    PostLoading,
    GeneralLoading,
    Avatar,
    PostHeader
  },
  methods: {
    translate: translate,
    onClick(i) {
      this.image_index = i;
    },
    async update_post(e) {
      //console.log("update content form post ", e);
      this.editing = false;
      //console.log("post_old", this.post);
      try {
        this.isLoading = true;
        await postRepository.updatePost(
          this.post._id,
          e.textContents,
          e.mediaContents
        );
        //console.log("cập nhật thành công");
        this.post = await postRepository.getPost(this.post._id);
        //console.log("post lấy từ server về:", this.post);
      } catch (e) {
        //console.log("cập nhập thất bại", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    },
    async cancel_edit() {
      this.editing = false;
      let id = this.postID;
      try {
        this.isLoading = true;
        this.post = await postRepository.getPost(id);
        //console.log("post", this.post);
      } catch (error) {
        //console.log("loadpost bị lỗi", error);
        EventBus.$emit("showError", {
          message: errorMessages["load_post_error"]
        });
      } finally {
        this.isLoading = false;
      }
    },
    edit_post() {
      this.editing = true;
    }
  }
};
</script>

<style>
.post {
  border-radius: 10px;
  background-color: white;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
}

.post-icons {
  color: black;
  /* width: 40px; */
  /* height: 40px; */
  /* margin: 10px; */
  /* padding: 2px; */
  /* border: solid 0px; */
  /* border-radius: 10px; */
}
.image-show {
  max-width: 300px;
  align-self: center;
}
.single-image {
  width: 100%;
  padding: 10px;
  height: auto;
}
.grid-image-double {
  width: 50%;
  padding: 10px;
  /* display: grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
  grid-gap: 1rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 5rem; */
}
.grid-image-tripple {
  width: 30%;
  margin: 10px;
  /* display: grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
  grid-gap: 1rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 5rem; */
}
.media-content {
  width: auto;
  border-radius: 10px;
  height: auto;
  object-fit: cover;
  border-radius: 0.75rem;
  display: inline-block;
}
.post-username {
  color: black;
}
.mini-text {
  font-size: 12px;
}
.post-avatar {
  border-radius: 10px;
  width: 40px;
  height: 40px;
  object-fit: cover; /* Do not scale the image */
  object-position: center; /* Center the image within the element */
}
.post-content-text {
  background-color: rgb(203, 226, 247);
  border-bottom-right-radius: 32px;
  border-bottom-left-radius: 32px;
  border-top-right-radius: 32px;
  border: 1px;
  padding: 10px;
  text-align: center;
  vertical-align: auto;
}
</style>
