<template>
  <div class="my-block image-editor fixed-top">
    <div>
      <cropper class="cropper" ref="cropper" :src="img"  @change="change"></cropper>
    </div>
    <button
      class="btn btn-warning mborder mt-2"
      @click="$emit('close')"
    >{{translate("Cancel")}}</button>
    <button class="btn btn-primary mborder mt-2" @click="crop">{{translate("Ok")}}</button>
  </div>
</template>

<script>
import VueCropper from "vue-cropperjs";
import { Cropper } from "vue-advanced-cropper";
import "cropperjs/dist/cropper.css";
import { translate } from "../../shared/translator";
export default {
  props: {
    img: {
      type: String
    },
    lang: {
      type: String,
      default: "en"
    }
  },
  components: {
    VueCropper
  },
  data() {
    return {
      cropImage: "",
      myfile:""
    };
  },
  methods: {
    translate: translate,
    async crop() {
      //console.log(this.myfile);
      this.$emit("change",this.myfile);
      this.$emit("close");
    },
    change({ coordinates, canvas }) {
      var self= this;
      canvas.toBlob(function(blob) {
        self.myfile=new File([blob], "image_edit");
      });
    }
  }
};
</script>

<style>
.image-editor {
  position: absolute;
  z-index: 99;
  position: absolute;
  top: 0;
  left: 0;
  background-position: center; /* Center the image */
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.89);
  height: 100%;
  width: 100%;
  text-align: center;
  vertical-align: middle;
  line-height: 200pt;
}

.cropper {
  text-align: center;
}
.image-crop{
  width: 10000px;
}
</style>