<template>
    <v-dialog v-model="dialog" persistent max-width="290">
      <template v-slot:activator="{ on, attrs }">
        <!-- <v-btn color="primary" dark v-bind="attrs" v-on="on">Open Dialog</v-btn> -->
        <v-btn icon color="red" class="mx-2" v-bind="attrs" v-on="on">
          <v-icon>mdi-delete</v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title class="headline">{{translate('delete_task_title')}}</v-card-title>
        <v-card-text>{{translate('delete_task_content')}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" text @click="dialog = false; $emit('onDelete')">{{translate("Delete")}}</v-btn>
          <v-btn color="green darken-1" text @click="dialog = false">{{translate("Cancel")}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
import { translate } from '../../shared/translator';
export default {
    data() {
        return {
            dialog: false
        }
    },
    methods: {
        translate:translate
    },
};
</script>

