<template>
  <v-expansion-panels v-model="show" :flat="true" class="mborder mr-5">
    <v-expansion-panel class="mborder">
      <v-expansion-panel-header>
        <h5>{{translate("Contents")}}</h5>
      </v-expansion-panel-header>
      <v-expansion-panel-content class="mborder">
        <ul class="list-group-sm no-padding-inline">
          <li
            class="list-group-item contentfilter"
            v-for="(content,index) in contents"
            v-bind:key="index"
          >
            {{translate(content.alias)}}
            <CheckboxElement class="float-right">
              <input type="checkbox" name id v-model="content.checked" />
            </CheckboxElement>
          </li>
        </ul>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import { translate } from "../../shared/translator";
import CheckboxElement from "../../shared/CheckboxElement";
import { SharedStorage } from "../../shared/event-bus";
export default {
  components: {
    CheckboxElement
  },
  data() {
    return {
      show:0,
      contents: [
        {
          name: "Hình ảnh",
          checked: false,
          alias: "Images"
        },
        {
          name: "Video",
          checked: false,
          alias: "Videos"
        },
        {
          name: "Văn bản",
          checked: false,
          alias: "Texts"
        },
        {
          name: "Đoạn ghi âm",
          checked: false,
          alias: "Audios"
        }
      ]
    };
  },
  watch: {
    contents: {
      deep: true,
      handler(newValue, oldValue) {
        this.updateContentFilter()
      }
    }
  },
  methods: {
    translate: translate,
    updateContentFilter() {
      let contentArr = [];
      for (let i = 0; i < this.contents.length; i++) {
        if (this.contents[i].checked == true)
          contentArr.push(this.contents[i].alias);
      }
      SharedStorage.currentFilter.contents = contentArr;
    }
  }
};
</script>

<style>
.list-group-item,
.contentfilter {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding-right: 20px;
}
</style>