<template>
  <div v-bind:style="styles" class="spinner mborder">
    <div v-bind:style="innerStyles" class="spinner-inner mborder">
      <div class="binding mborder"></div>
      <div class="pad">
        <div class="line line1 mborder"></div>
        <div class="line line2 mborder"></div>
        <div class="line line3 mborder"></div>
        <div class="line line4 mborder"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    size: {
      default: 40
    }
  },
  computed: {
    innerStyles() {
      let size = parseInt(this.size);
      return {
        transform: "scale(" + size / 31 + ")"
      };
    },
    styles() {
      return {
        // width: (this.size * 2) + "px",
        // height: this.size + "px"
      };
    }
  }
};
</script>
<style lang="scss" scoped>
.spinner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  * {
    line-height: 0;
    box-sizing: border-box;
  }
}
.spinner-inner {
  flex-shrink: 0;
  width: 80%;
  height: 80%;
}
.spinner-inner .binding {
  content: "";
  width: 100%;
//   height: 4px;
//   border: 2px solid #a6d28e;
  margin: 0 auto;
}
.spinner-inner .pad {
  width: 100%;
  height: 100%;
//   border: 2px solid #a6d28e;
  border-top: 0;
  padding: 4px;
  margin: 0 auto;
}
.spinner-inner .line {
  width: 90%;
  height: 15%;
  margin-top: 5%;
  background-color: #c0f0fc;
  opacity: 0;
  animation: mikepad-writeline 3s infinite ease-in;
}
.spinner-inner .line:first-child {
  margin-top: 0;
}
.spinner-inner .line.line1 {
  animation-delay: 0s;
}
.spinner-inner .line.line2 {
  animation-delay: 0.5s;
}
.spinner-inner .line.line3 {
  animation-delay: 1s;
}
.spinner-inner .line.line4 {
  animation-delay: 1.5s;
}
.spinner-inner .text {
  text-align: center;
  margin-top: 10px;
  font-size: 14px;
  color: #a6d28e;
}
@keyframes mikepad-writeline {
  0% {
    width: 0px;
    opacity: 0;
  }
  33% {
    width: 15px;
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
</style>