<template>
  <div class="password-div">
    <div>
      <p>{{translate('set_password_str')}}</p>
      <input type="password" class="mborder form-control" v-model="password" />
      <password class="mx-2" v-model="password" :strength-meter-only="true" />
    </div>
    <div>
      <p>{{translate('verify_password_str')}}</p>
      <input
        type="password"
        :class="{'mborder':true, 'form-control':true,'wrong_password':!is_verified_password}"
        v-model="verify_password"
        @keyup="setComplete"
      />
    </div>
    <div class="row">
      <div class="col d-sm-none"></div>
      <div class="col">
        <button class="row btn btn-primary mborder float-right mt-2 mr-1 btn-sm" :disabled="!is_verified_password" @click="emitPass">{{translate('saveSetting')}}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { translate } from "../../shared/translator";
import Password from "vue-password-strength-meter";
import { encryptPass } from "../../shared/securityModel";
export default {
  components: {
    Password
  },
  watch: {
    password: function(val) {
      if (val === this.verify_password) {
        this.is_verified_password = true;
      } else {
        this.is_verified_password = false;
      }
    },
    verify_password: function(val) {
      if (val === this.password) {
        this.is_verified_password = true;
      } else {
        this.is_verified_password = false;
      }
    },
    
  },
  data() {
    return {
      password: "",
      verify_password: "",
      is_verified_password: true
    };
  },
  methods: {
    translate: translate,
    emitPass(){
        let pass= encryptPass(this.password);
        this.$emit("save",pass);
    },
    setComplete(e){
      if(e.key=="Enter" && this.is_verified_password)
      this.emitPass();
    }
  },
  props: {
    lang: {
      type: String,
      default: "en"
    },
    show: Boolean
  }
};
</script>

<style>
.password-div {
  border: 1px dashed #a8a8a8;
  border-radius: 10px;
  background-color: #fff;
  margin: 10px;
  padding: 20px;
}
.wrong_password {
  border: solid red 1px !important;
}
</style>