<template>
    <div
      class="modal fade mborder"
      id="freezeModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="freezeModalTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <icon icon="snowflake" size="lg" class="float-left mx-3 mt-1"></icon>
            <h5
              class="modal-title"
              id="freezeModalTitle"
            >
              {{ freeze_modal_title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{ freeze_modal_body }}
          </div>
          <div class="modal-footer">
            <button
              @click="$emit('freeze')"
              type="button"
              class="btn btn-primary mborder"
            >
              {{ freeze_btn_str }}
            </button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { translate } from '../../shared/translator';
import { SharedStorage } from '../../shared/event-bus';
export default {
  props:{
    lang:{
      type:String,
      default:"en"
    },
    freeze:Boolean
  },
  data() {
    return {
      freeze_modal_title: (!SharedStorage.settings.freezeSettings.showFreezeDiary)?translate('freeze_modal_title'):translate('unfreeze_modal_title'),
      freeze_modal_body:(!SharedStorage.settings.freezeSettings.showFreezeDiary)?translate('freeze_modal_body'):translate('unfreeze_modal_body'),
      freeze_btn_str: (!SharedStorage.settings.freezeSettings.showFreezeDiary)?translate("Freeze"):translate("Warm_up")
    }
  }
};
</script>

<style></style>
