<template>
  <div class="editor" @dragover="on_drag">
    <div class="drag-upload-cover" v-if="isDragover" @drop="onDrop" @dragleave="dragleave">
      <div class="centered full-width text-center text-primary">
        <svg class="icon-drag-drop" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
          <icon icon="file" />
        </svg>
        <h4 class="drop-text-here">
          <b>{{translate('drop_file_here')}}</b>
        </h4>
      </div>
    </div>
    <DisabledElement v-if="isLoading"></DisabledElement>
    <div class="input-group">
      <Avatar class="avatar mr-2 mt-1" :src="user.avatar" :username="user.name" :size="40" />
      <input
        type="text"
        class="form-control mborder my-2"
        :placeholder="(editMode)?translate('add_event_name_for_diary'):translate('what_happen_today', lang)"
        v-model="textContents.event_name"
        @input="changeStatus"
      />
    </div>
    <TextareaAutosize
      v-if="textContents.event_name != ''"
      class="input-new-post form-control"
      :placeholder="(editMode)?translate('add_title_for_diary'):translate('add_title_for_diary' )"
      v-model="textContents.title"
      @input="changeStatus"
      @paste_image="paste_image"
    ></TextareaAutosize>
    <hr />

    <topic-chooser
      :choosed="(textContents.topic.topicID != undefined)?textContents.topic.topicID:''"
      v-show="is_browsing_type[4].status"
      class="audios-div"
      @topic_changed="updateTopic"
      @delete_topic="deleteTopic"
    ></topic-chooser>
    <div class="row">
      <vue-feedback-reaction
        @input="updateFeeling"
        class="audios-div justify-content-center"
        :value="textContents.feeling"
        v-show="is_browsing_type[3].status"
      />
      <!-- <div class="audios-div col justify-content-center" v-show="is_browsing_type[2].status">
        <AudioUpload></AudioUpload>
        <div v-if="audioSources.length">
          <audio
            :src="audioSources[currentAudioIndex]"
            controls
            style="display:inline-block; outline:none !important; border:none; width:90%"
          ></audio>
          <button class="close" style="outline:none !important" @click="deleteAudio">
            <icon icon="trash-alt" size="md" class="npost-icons float-left mt-2 mr-2" />
          </button>
        </div>
        <div class="mb-2">
          <button
            v-for="(audio,i) in audioSources"
            v-bind:key="'audio'+i"
            :class="'mx-2 my-2 btn solidBorder mborder'"
            @click="currentAudioIndex=i"
          >
            <icon v-if="!audioSources.length" icon="file-audio" size="lg" class="npost-icons" />
            <icon v-else icon="file-audio" size="sm" class="tag-icon float-left ml-2" />
            <span class="number mr-2 ml-1">{{i+1}}</span>
          </button>
        </div>
      </div> -->
    </div>
    <div>
      <ImageUpload
        class="mb-2"
        v-show="is_browsing_type[0].status"
        @images_changed="updateImages"
        @deleteImage="deleteImage"
        @limit-exceeded="limitErrorShow"
        @changeHighLight="changeImageHighLight"
        @edit="editImage"
        :showPrimary="false"
        :images="imageSources"
        :idUpload="editMode?(post._id+'imageUpload'):('newPostImageUpload')"
        :idEdit="editMode?(post._id+'imageEdit'):('newPostImageUpload')"
      ></ImageUpload>
      <VideoUpload
        class="mb-2"
        v-show="is_browsing_type[1].status"
        :showPrimary="false"
        @videos_changed="updateVideos"
        @deleteVideo="deleteVideo"
        @changeHighLight="changeVideoHighLight"
        @limit-exceeded="limitErrorShow"
        :idUpload="editMode?(post._id+'videoUpload'):('newPostVideoUpload')"
        :idEdit="editMode?(post._id+'videoEdit'):('newPostVideoUpload')"
        :videos="videoSources"
      ></VideoUpload>
      <button class="m-btn mr-2 mb-2" @click="browse('image')">
        <icon
          v-if="!imageSources.length"
          icon="image"
          size="lg"
          class="npost-icons float-left mx-2"
        />
        <icon v-else icon="image" size="sm" class="tag-icon float-left mx-2" />

        <span class="mr-2">{{translate("Images")}}</span>
        <span
          class="float-right number-media-contents"
          v-show="imageSources.length>0"
        >+{{imageSources.length}}</span>
      </button>
      <button class="m-btn mr-2 mb-2" @click="browse('video')">
        <icon
          v-if="!videoSources.length"
          icon="video"
          size="lg"
          class="npost-icons float-left mx-2"
        />
        <icon v-else icon="video" size="sm" class="tag-icon float-left mx-2" />

        <span class="mr-2">{{translate("Videos")}}</span>
        <span
          class="float-right number-media-contents"
          v-show="videoSources.length>0"
        >+{{videoSources.length}}</span>
      </button>
      <!-- <button class="m-btn mr-2 mb-2" @click="browse('audio')">
        <icon
          v-if="!audioSources.length"
          icon="microphone"
          size="lg"
          class="npost-icons float-left mx-2"
        />
        <icon v-else icon="microphone" size="sm" class="tag-icon float-left mx-2" />

        <span class="mr-2">{{translate("Record")}}</span>
        <span
          class="float-right number-media-contents"
          v-show="audioSources.length>0"
        >+{{audioSources.length}}</span>
      </button> -->
      <button class="m-btn mr-2 mb-2" @click="browse('feeling')">
        <icon
          v-show="(textContents.feeling=='')"
          icon="laugh"
          size="lg"
          class="npost-icons float-left mx-2"
        />
        <span class="mr-2">{{translate("Feelings")}}</span>
        <img
          v-show="textContents.feeling!=''"
          width="30px"
          class="npost-icons"
          :src="feelingArr[textContents.feeling]"
          alt
        />
      </button>
      <button class="m-btn mb-2" @click="browse('topic')">
        <icon
          v-if="textContents.topic==''"
          icon="tags"
          size="lg"
          class="npost-icons float-left mx-2"
        />
        <icon v-else icon="tags" size="sm" class="tag-icon float-left mx-2" />
        <span
          class="mr-2"
        >{{(textContents.topic=='')?translate("Topics"):translate(textContents.topic.title)}}</span>
      </button>
      <!-- <button class="m-btn">
        <icon icon="ellipsis-h" class="icons float-left ml-2" />
        <span>More</span>
      </button>-->
    </div>
    <button
      v-if="!editMode"
      class="btn btn-primary mborder form-control mt-2"
      v-bind:disabled="!haveContent"
      v-show="haveContent"
      @click="postToServer"
    >
      <span>{{translate("Post")}}</span>
    </button>
    <div class="row">
      <div class="col"></div>
      <div class="col-auto">
        <general-loading v-if="isLoading" class="my-center"></general-loading>
      </div>
      <div class="col"></div>
    </div>
    <div v-if="editMode" class="row">
      <div class="col">
        <button
          class="btn btn-primary mborder form-control mt-2"
          v-bind:disabled="!haveContent"
          @click="updatePost"
        >{{translate("save_change")}}</button>
      </div>
      <div class="col">
        <button
          class="btn btn-warning mborder form-control mt-2"
          @click="$emit('edit_canceled')"
        >{{translate("Cancel")}}</button>
      </div>
    </div>
    <!-- <ImageEditor :include-ui="useDefaultUI" :options="options"></ImageEditor> -->
    <ImageCropper
      :img="imageEditing"
      v-if="editingImage"
      @close="editingImage=false"
      @change="updateImagesFile"
    ></ImageCropper>
  </div>
</template>

<script>
import AudioUpload from "./AudioUpload";
import GeneralLoading from "../../shared/GeneralLoading";
import ImageUpload from "./ImageUpload";
import VideoUpload from "./VideoUpload";
import VueFeedbackReaction from "../../shared/VueFeedbackReaction";
import postRepository from "../../../repositories/postRepository";
import Loading from "vue-loading-overlay";
import { library } from "@fortawesome/fontawesome-svg-core";
import GoodActive from "../../shared/assets/good_active.png";
import HateActive from "../../shared/assets/hate_active.png";
import NaturalActive from "../../shared/assets/natural_active.png";
import ExcellentActive from "../../shared/assets/excellent_active.png";
import DisappointedActive from "../../shared/assets/disappointed_active.png";
import Dictaphone from "./Dictaphone";
import SpectrumAnalyser from "./SpectrumAnalyser";
import { errorMessages } from "../../shared/errorMessage";
import TopicChooser from "./TopicChooser";
import DisabledElement from "../../shared/DisabledElement";
import TextareaAutosize from "./TextareaAutosize";
import { translate } from "../../shared/translator";
import { SharedStorage } from "../../shared/event-bus";
import ImageCropper from "./ImageCropper";
import Avatar from "vue-avatar";
// import ImageEditor from "@toast-ui/vue-image-editor";

import {
  faTrashAlt,
  faImage,
  faVideo,
  faMicrophone,
  faLaugh,
  faEllipsisH,
  faFileAudio,
  faTags,
  faFile
} from "@fortawesome/free-solid-svg-icons";
import { EventBus } from "../../shared/event-bus";

library.add(
  faImage,
  faVideo,
  faMicrophone,
  faLaugh,
  faEllipsisH,
  faTrashAlt,
  faFileAudio,
  faTags,
  faFile
);
let vm = this;
export default {
  beforeMount() {
    this.user = SharedStorage.currentUser;
    if (this.editMode == true && this.post != undefined) {
      //console.log("edit mode");
      this.textContents.title = this.post.title;
      this.textContents.event_name = this.post.event_name;
      this.textContents.feeling = this.post.feeling;
      this.oldImageSrc = this.post.images;
      this.oldVideoSrc = this.post.videos;
      this.oldAudioSrc = this.post.audios;
      this.textContents.topic = this.post.topic;
    }
  },
  mounted() {
    [
      "drag",
      "dragstart",
      "dragend",
      "dragover",
      "dragenter",
      "dragleave",
      "drop"
    ].forEach(event => {
      window.addEventListener(event, e => {
        //console.log(event, "catch in window");
        if (event == "dragover") {
          this.isDragover = false;
        }
        if (event == "dragenter") {
          // this.isDragover=true;
        }
        e.preventDefault();
        e.stopPropagation();
      });
    });
    // document.body.addEventListener("dragleave", event => {
    //   //console.log("dragleave");
    //   event.stopPropagation();
    //   event.preventDefault();
    //   this.isDragover = false;
    // });
  },
  computed: {
    imageSources: function() {
      let imgArr = [];
      this.oldImageSrc.forEach((image, index) => {
        imgArr.push({
          path: image,
          highlight: index == this.currentHighLightImage ? 1 : 0,
          default: 0
        });
      });
      this.mediaContents.images.forEach((element, index) => {
        imgArr.push({
          path: URL.createObjectURL(element),
          highlight:
            index + this.oldImageSrc.length == this.currentHighLightImage
              ? 1
              : 0,
          default: 0
        });
      });
      return imgArr;
    },
    videoSources: function() {
      let vidArr = [];
      this.oldVideoSrc.forEach((video, index) => {
        vidArr.push({
          path: video,
          highlight: index == this.currentHighLightVideo ? 1 : 0,
          default: 1
        });
      });
      this.mediaContents.videos.forEach((element, index) => {
        vidArr.push({
          name: element.name,
          path: URL.createObjectURL(element),
          highlight:
            index + this.oldVideoSrc.length == this.currentHighLightVideo
              ? 1
              : 0,
          default: 1
        });
      });
      return vidArr;
    },
    audioSources: function() {
      let audArr = [];
      this.oldAudioSrc.forEach(audio => {
        audArr.push(audio);
      });
      this.mediaContents.audios.forEach(element => {
        audArr.push(element.src);
      });
      return audArr;
    },
    notBrowsing: function() {
      this.is_browsing_type.forEach(type => {
        //console.log(`${type.name}, ${type.status}`);
        if (type.status) return false;
      });
      return true;
    }
  },
  components: {
    AudioUpload,
    ImageUpload,
    VideoUpload,
    VueFeedbackReaction,
    GeneralLoading,
    TopicChooser,
    Dictaphone,
    SpectrumAnalyser,
    DisabledElement,
    TextareaAutosize,
    ImageCropper,
    Avatar
    // ImageEditor
  },
  props: {
    post: { type: Object, default: null },
    editMode: Boolean,
    lang: { type: String, default: "en" }
  },
  data() {
    return {
      //for image editor
      editingImage: false,
      imageEditing: "",
      //
      user: {},
      isDragEnter: false,
      isDragover: false,
      showError: false,
      isLoading: false,
      haveContent: false,
      currentAudioIndex: 0,
      currentHighLightImage: 0,
      currentHighLightVideo: 0,
      feelingArr: [
        ExcellentActive,
        GoodActive,
        NaturalActive,
        DisappointedActive,
        HateActive
      ],
      is_browsing_type: [
        {
          name: "image",
          status: false
        },
        {
          name: "video",
          status: false
        },
        {
          name: "audio",
          status: false
        },
        {
          name: "feeling",
          status: false
        },
        {
          name: "topic",
          status: false
        }
      ],
      textContents: {
        event_name: "",
        title: "",
        feeling: "",
        topic: ""
      },
      mediaContents: {
        images: [],
        videos: [],
        audios: []
      },
      currentTopic: "",
      //sử dụng trong trường hợp chỉnh sửa post
      oldImageSrc: [],
      oldVideoSrc: [],
      oldAudioSrc: []
    };
  },
  methods: {
    translate: translate,
    limitErrorShow(e) {
      //console.log(e.amount, "/", e.max, "(", e.type, ")");
    },
    updateImagesFile(file) {
      //xóa bỏ file cũ và gửi file mới cho ImageUpload (mượn hàm add_image_via_drop vì chưa thấy cần hàm mới)
      if (this.currentHighLightImage >= this.oldImageSrc.length) {
        this.mediaContents.images.splice(
          this.currentHighLightImage - this.oldImageSrc.length,
          1
        );
      } else {
        this.oldImageSrc.splice(this.currentHighLightImage, 1);
      }
      EventBus.$emit("add_image_via_drop", file);
    },
    // handleError() {
    //   //TODO hiển thị lỗi không bật được mic ghi âm
    //   this.showError = true;
    // },
    paste_image(files) {
      this.mediaContents.images = this.mediaContents.images.concat(files);
      this.browse("image");
      this.changeStatus();
    },
    // eslint-disable-next-line no-unused-vars
    // handleRecording({ blob, src }) {
    //   //console.log("new audio");
    //   this.audioSources.push(src);
    //   this.mediaContents.audios.push({ blob: blob, src: src });
    //   this.changeStatus();
    // },
    browse(str) {
      // //console.log("type ", str);
      this.is_browsing_type.forEach((type, i) => {
        if (type.name == str) this.is_browsing_type[i].status = !type.status;
        else this.is_browsing_type[i].status = false;
      });
      // //console.log(this.is_browsing_type);
    },
    postToServer() {
      // //console.log(this.userPost);
      this.isLoading = true;
      postRepository
        .uploadPost(this.textContents, this.mediaContents)
        .then(r => {
          console.log("đăng bài thành công", r);
          this.textContents.title = "";
          this.textContents.feeling = "";
          this.textContents.topic = "";
          this.currentTopic = "";
          this.mediaContents.images = [];
          this.mediaContents.videos = [];
          this.mediaContents.audios = [];
          this.is_browsing_type.forEach(type => {
            type.status = false;
          });
          this.changeStatus();
          this.$emit("new_post_created");
          this.isLoading = false;
        })
        .catch(e => {
          console.log("đăng bài thất bại", e);
          this.$emit("showPopup", errorMessages.errorCreate);
        });
    },
    updateImages(imageList) {
      //console.log("data", imageList);
      this.mediaContents.images = imageList;
      this.changeStatus();
    },
    updateTopic(topic) {
      delete topic["checked"];
      this.textContents.topic = topic;
      // //console.log(topic);
    },
    updateVideos(videoList) {
      //console.log(videoList);
      this.mediaContents.videos = videoList;
      this.changeStatus();
    },
    updateFeeling(e) {
      if (e == -1) {
        this.textContents.feeling = "";
        this.browse("feeling");
        return;
      }
      this.textContents.feeling = e;
    },
    deleteTopic(topic) {
      if (topic.topicID == this.textContents.topic.topicID) {
        this.textContents.topic = "";
      }
    },
    deleteVideo(index) {
      if (index >= this.oldVideoSrc.length) {
        //console.log("xóa videos tại vị trí", index);

        //xóa video mới thêm vào
        //console.log("xóa videos mới thêm vào");
        // this.mediaContents.videos.splice(index - this.oldVideoSrc.length, 1);
      } else {
        //xóa video vốn có của bải viết
        this.oldVideoSrc.splice(index, 1);
      }
    },
    deleteAudio() {
      //console.log("delete audio at", this.currentAudioIndex);
      if (this.currentAudioIndex >= this.oldAudioSrc.length) {
        //xóa audio mới thêm vào
        //console.log("xóa audio mới");

        this.mediaContents.audios.splice(
          this.currentAudioIndex - this.oldAudioSrc.length,
          1
        );
      } else {
        //xóa audio vốn có của bải viết
        //console.log("xóa audio gốc");

        this.oldAudioSrc.splice(this.currentAudioIndex, 1);
      }
      this.currentAudioIndex = 0;
      // this.audioSources.splice(this.currentAudioIndex, 1);
      // this.mediaContents.audios.splice(this., 1);
      this.changeStatus();
    },
    deleteImage(index) {
      // //console.log("xóa ảnh tại vị trí", index)
      if (index >= this.oldImageSrc.length) {
        //xóa ảnh mới thêm vào
        //comment dòng dưới vì đã xóa ở trong imageUpload rồi -> fix lỗi xóa 2 file
        // this.mediaContents.images.splice(index - this.oldImageSrc.length, 1);
      } else {
        //xóa ảnh vốn có của bải viết
        this.oldImageSrc.splice(index, 1);
      }
    },
    changeStatus() {
      if (
        this.textContents.event_name.trim() != "" ||
        this.mediaContents.images.length > 0 ||
          this.mediaContents.videos.length > 0 ||
          this.mediaContents.audios.length > 0 ||
          this.textContents.feeling != ""
      ) {
        this.haveContent = true;
      } else this.haveContent = false;
    },
    changeVideoHighLight(e) {
      this.currentHighLightVideo = e;
    },
    changeImageHighLight(e) {
      this.currentHighLightImage = e;
    },
    updatePost(e) {
      this.isLoading = true;
      //console.log("Chưa gửi nội dung lên server");
      let newTextContents = this.textContents;
      //console.log("old image src", this.oldImageSrc);
      newTextContents.videos = this.oldVideoSrc;
      newTextContents.audios = this.oldAudioSrc;
      newTextContents.images = this.oldImageSrc;
      //console.log("textContents from Editor:", newTextContents);
      this.$emit("post_updated", {
        textContents: newTextContents,
        mediaContents: this.mediaContents
      });
    },
    on_drag(e) {
      e.stopPropagation();
      e.preventDefault();
      // //console.log("not browsing", this.notBrowsing);
      if (this.notBrowsing) this.isDragover = true;
    },
    onDrop(e) {
      e.stopPropagation();
      e.preventDefault();
      let files = e.dataTransfer.files;
      if (!files.length) {
        return false;
      }
      let haveWrongFile = false;
      let haveImage = false;
      let haveVideo = false;
      for (let i = 0; i < files.length; ++i) {
        let file = files[i];
        let type = this.isAcceptFile(file.name);
        if (type == null) haveWrongFile = true;
        if (type == "image") {
          // this.mediaContents.images.push(file);
          EventBus.$emit("add_image_via_drop", file);
        }
        if (type == "video") {
          // this.mediaContents.videos.push(file);
          EventBus.$emit("add_video_via_drop", file);
        }
      }
      if (haveWrongFile) {
        EventBus.$emit("showError", errorMessages["wrong_file"]);
      }
      if (haveImage) {
        this.browse("image");
      }
      if (haveVideo) {
        this.browse("video");
      }
      this.isDragover = false;
    },
    isAcceptFile(filename) {
      var _validImageExtensions = [".jpg", ".jpeg", ".bmp", ".gif", ".png"];
      var _validVideoExtensions = [".mp4"];
      for (var j = 0; j < _validImageExtensions.length; j++) {
        var sCurExtension = _validImageExtensions[j];
        if (
          filename
            .substr(
              filename.length - sCurExtension.length,
              sCurExtension.length
            )
            .toLowerCase() == sCurExtension.toLowerCase()
        ) {
          return "image";
        }
      }
      for (var j = 0; j < _validVideoExtensions.length; j++) {
        var sCurExtension = _validVideoExtensions[j];
        if (
          filename
            .substr(
              filename.length - sCurExtension.length,
              sCurExtension.length
            )
            .toLowerCase() == sCurExtension.toLowerCase()
        ) {
          return "video";
        }
      }
      return null;
    },
    dragleave(e) {
      //console.log("dragleave");

      e.preventDefault();
      e.stopPropagation();
      this.isDragover = false;
    },
    editImage(e) {
      //console.log("edit ", e, this.oldImageSrc.length);
      if (e >= this.oldImageSrc.length) {
        this.imageEditing = URL.createObjectURL(
          this.mediaContents.images[e - this.oldImageSrc.length]
        );
      } else {
        this.imageEditing = this.oldImageSrc[e];
        //console.log("here");
      }
      this.currentHighLightImage = e;
      this.editingImage = true;
    }
  }
};
</script>

<style>
.editor {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
  position: relative;
  /* margin-inline: 10px; */
}
.m-btn {
  /* font-size: 30px; */
  border-radius: 10px;
  background-color: whitesmoke !important;
  border: none;
  outline: none !important;
  padding: 10px;
  /* margin: 20px; */
}
.m-btn:hover {
  /* font-size: 30px; */
  border-radius: 10px;
  background-color: rgb(140, 201, 255) !important;
  border: none;
  outline: none !important;
  padding: 10px;
  cursor: pointer;
  /* margin: 20px; */
}
.npost-icons {
  color: black;
  /* width: 40px; */
  /* height: 40px; */
  margin: auto;
  /* border: solid 1px; */
  /* border-radius: 10px; */
}
.input-new-post {
  border-radius: 10px !important;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.btn-post-submit {
  background-color: #91c771;
  color: white;
}
