<template>
  <div class="px-2">
    <VuetifyDateTimePicker
      v-model="datetime"
      :text-field-props="textFieldProps"
      :date-picker-props="dateProps"
      :time-picker-props="timeProps"
      :label="label"
      time-format="HH:mm"
      @input="update"
    ></VuetifyDateTimePicker>
  </div>
</template>

<script>
import VuetifyDateTimePicker from "./VuetifyDateTimePicker";
import { translate } from "../../shared/translator";
export default {
  methods: {
    update() {
      //console.log(this.datetime);
      if(this.datetime instanceof Date){
        this.datetime = this.datetime.getTime();
      }
      this.$emit("input", this.datetime);
    }
  },
  data() {
    return {
      nullDatetime: null,
      datetime: null,
      textFieldProps: {
        prependIcon: "event"
      },
      label: translate("custom"),
      dateProps: {},
      timeProps: {
        format: "24hr"
      }
    };
  },
  props: {
    value: {
      type: Date|String|Number,
      default: null
    }
  },
  components: {
    VuetifyDateTimePicker
  }
};
</script>

