<template>
  <div class="my-block">
    <ul class="list-group-sm no-padding-inline">
      <li class="list-group-item mx-2" v-for="(setting, i) in notificationSettings" v-bind:key="i">
        <div class="row">
          <div class="col-10 no-padding-inline">
            <span class="ml-1">{{ translate(setting.title)}}</span>
          </div>
          <div class="col-2 no-padding-inline">
            <SwitchElement
            class="mt-1"
              v-bind:checked="setting.checked"
              @updated="setting.checked = !setting.checked"
            ></SwitchElement>
          </div>
        </div>
        <ul v-if="setting.checked" class="list-group-sm no-padding-inline">
          <li class="list-group-item mx-2" v-for="(option, i) in setting.options" v-bind:key="i">
            <span>{{ translate(option.title) }}</span>
            <input
              v-if="option.type == 'frequency'"
              class="input-number"
              type="number"
              v-model="option.value"
            />
            <span v-if="option.type == 'frequency'">{{ translate(option.unit) }}</span>
            <CheckboxElement class="float-right" v-model="option.checked"></CheckboxElement>
          </li>
        </ul>
      </li>
      <hr />
      <li class="list-group-item mx-2" v-for="(v, i) in noticeMethods" v-bind:key="'method'+i">
        <span>{{ translate(v.method) }}</span>
        <CheckboxElement class="float-right mx-2" v-model="v.checked"></CheckboxElement>
      </li>
    </ul>
    <div class="row">
      <div class="col d-sm-none"></div>
      <div class="col">
        <button
          class="btn btn-primary mborder float-right btn-sm mx-2"
          @click="update_reminder_setting"
        >{{translate("saveSetting")}}</button>
      </div>
    </div>
  </div>
</template>

<script>
import SwitchElement from "../../shared/SwitchElement";
import { translate } from "../../shared/translator.js";
import CheckboxElement from "../../shared/CheckboxElement";
// var dict = require("../../shared/translator.js").dictionary;
export default {
  data() {
    return {
      notificationSettings: [
        {
          title: "remind_write_diary",
          checked: this.settings.reminder.diary_reminder.turnOn,
          options: [
            {
              checked: this.settings.reminder.diary_reminder.options
                .remind_every.turnOn,
              title: "every_day",
              type: "time",
              value: this.settings.reminder.diary_reminder.options.remind_every
                .value
            },
            {
              type: "frequency",
              checked: this.settings.reminder.diary_reminder.options
                .remind_when_no_active.turnOn,
              unit: this.settings.reminder.diary_reminder.options
                .remind_when_no_active.unit,
              title: "after_no_active",
              value: this.settings.reminder.diary_reminder.options
                .remind_when_no_active.value
            }
          ]
        },
        {
          title: "new_device_reminder",
          checked: this.settings.reminder.new_device_reminder.turnOn,
          options: []
        },
        {
          title: "review_topic_reminder",
          checked: this.settings.reminder.topic_reminder.turnOn,
          options: this.userTopic
        },
        {
          title: "send_me_user_tips",
          checked: false,
          options: []
        }
      ],
      noticeMethods: [
        {
          method: "viaEmail",
          checked: this.settings.methods.viaEmail
        },
        {
          method: "viaApp",
          checked: this.settings.methods.viaApp
        }
      ]
    };
  },
  components: {
    SwitchElement,
    CheckboxElement
  },
  methods: {
    translate: translate,
    log_info() {
      //console.log("adfasdfads", this.settings);
    },
    update_reminder_setting() {
      let notificationSettingObject = {
        reminder: {
          diary_reminder: {
            turnOn: this.notificationSettings[0].checked,
            options: {
              remind_every: {
                turnOn: this.notificationSettings[0].options[0].checked,
                value: this.notificationSettings[0].options[0].value
              },
              remind_when_no_active: {
                turnOn: this.notificationSettings[0].options[1].checked,
                unit: this.notificationSettings[0].options[1].unit,
                value: this.notificationSettings[0].options[1].value
              }
            }
          },
          new_device_reminder: {
            turnOn: this.notificationSettings[1].checked
          },
          topic_reminder: {
            turnOn: this.notificationSettings[2].checked,
            //TODO thêm các topic được tick vào danh sách đưa lên server
            turnedOnTopic: this.getCheckedTopics()
          }
        },
        methods: {
          viaEmail: this.noticeMethods[0].checked,
          viaApp: this.noticeMethods[1].checked
        }
      };
      this.$emit("update", notificationSettingObject);
    },
    getCheckedTopics() {
      let checkedTopics = [];
      this.notificationSettings[2].options.forEach(topic => {
        if (topic.checked) {
          checkedTopics.push(topic.topicID);
        }
      });
      return checkedTopics;
    }
  },
  props: {
    settings: Object,
    lang: {
      type: String,
      default: "en"
    },
    userTopic: {
      default: [],
      type: Array
    }
  }
};
</script>

<style>
.input-number {
  outline: none;
  width: 60px;
  border-radius: 10px;
  text-align: center;
}
.right-bottom {
  margin-right: 0px;
  margin-bottom: 10px;
}
</style>
