<template>
  <div>
    <TaskView v-if="activeBody=='task'" />
    <PostView v-else />
  </div>
</template>

<script>
import PostView from "./PostView";
import TaskView from "./TaskView";
import { EventBus } from "../shared/event-bus";
export default {
  beforeCreate() {
    EventBus.$on("viewChanged", view => {
      switch (view) {
        case "task":
          this.activeBody = "task";
          break;

        default:
          this.activeBody = "post";
          break;
      }
    });
  },
  props: {
    lang: {
      type: String,
      default: "en"
    },
    filter: Object,
    user: Object
  },
  data() {
    return {
      activeBody: "post"
    };
  },
  components: {
    PostView,
    TaskView
  }
};
</script>

<style>
.scroll_body {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 90vh;
}
.scroll_body::-webkit-scrollbar {
  width: 0px;
}
</style>
