<template>
  <div
    v-if="show"
    :class="{'my-popup':!full_screen,  'container':!full_screen, 'my-popup-full-screen':full_screen}"
    :style="'background-image:url('+background_image+')'"
  >
    <div v-bind:class="{'my-block my-center':true,'my-shake-animation' : wrong}">
      <div class="container-fluid">
        <h4 style="text-align:center">{{ translate(header) }}</h4>
        <p>{{ translate(title) }}</p>
        <input
          type="password"
          v-model="input_password"
          v-bind:class="{'form-control my-border-radius mt-2':true, 'wrong-input' : wrong}"
          @keyup="checkEnter"
        />
        <button @click="checkPassword" class="btn btn-primary my-btn mt-2 float-right">{{ ok }}</button>
      </div>
    </div>
  </div>
</template>

<script>
import { encryptPass } from "../shared/securityModel";
import { translate } from "./translator.js";
export default {
  data() {
    return {
      header: "verify_required",
      ok: "Verify",
      wrong: false,
      input_password: "",
      background_image:
        "https://images.unsplash.com/photo-1471107340929-a87cd0f5b5f3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=966&q=80"
    };
  },
  props: {
    lang: {
      type: String,
      default: "en"
    },
    title: String,
    show: Boolean,
    caller_id: String,
    password: String,
    full_screen: Boolean
  },
  methods: {
    translate: translate,
    checkPassword() {
      //console.log("checkpassword");
      if (encryptPass(this.input_password) == this.password) {
        this.input_password = "";
        this.$emit("correct");
      } else {
        //console.log(this.wrong);
        this.wrong = true;
        this.$emit("incorrect");
      }
    },
    checkEnter(e) {
      //console.log(e);
      if (e.key == "Enter") this.checkPassword();
      else this.wrong = false;
    }
  }
};
</script>

<style>
.my-popup {
  background-position: center; /* Center the image */
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  width: 92%;
  margin-right: 20px;
  border-radius: 10px;
  position: absolute;
  top: 1px;
  z-index: 990;
  left: 19px;
  text-align: center;
  /* display: inline-block; */
}
.my-popup-full-screen {
  background-position: center; /* Center the image */
  background-repeat: no-repeat;
  background-size: cover;
  /* position:fixed; */
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  text-align: center;
  /* display: inline-block; */
}

.my-btn {
  border-radius: 10px;
}
.my-border-radius {
  border-radius: 10px;
}
.wrong-input {
  border: solid 1px red !important;
}
.my-shake-animation {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}
@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
</style>
