<template>
  <div class="scroll_body" @scroll="load_more_post">
    <button hidden id="showDeleteModalBtn" data-toggle="modal" data-target="#deleteModal"></button>
    <button hidden id="showShareModalBtn" data-toggle="modal" data-target="#shareModal"></button>
    <button hidden id="showFreezeModalBtn" data-toggle="modal" data-target="#freezeModal"></button>
    <DeletePostModal
      
      @delete="delete_post"
      @freeze="callDeleteAction();callFreezeAction()"
      @edit="callDeleteAction(),callEditAction()"
    ></DeletePostModal>
    <!-- TODO đổi lại cách lấy shareLink từ post -->
    <SharePostModal
      
      v-if="posts.length"
      @commit_share="share_post"
      :shareLink="posts[active_index].shareLink"
      :shareOn="posts[active_index].shareLinkStatus"
      @updateShareLinkStatus="updateShareLinkStatus"
    ></SharePostModal>
    <FreezePostModal  :freeze="isFreezeAction" @freeze="freeze_post"></FreezePostModal>
    <NewPostEditor
      
      class="shadow"
      :user="user"
      @new_post_created="refreshPosts"
      :editMode="false"
    ></NewPostEditor>
    <div style="width:100%; height:100%" v-if="isLoading">
      <div v-for="(l,i) in [0]" :key="i" class="body-loading my-3">
        <div class="row" style="width:100%; height:100%">
          <div class="col-2">
            <general-loading :color="'#c0f0fc'"></general-loading>
          </div>
          <div class="col-10">
            <post-loading></post-loading>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="my-3" v-for="(post, index) in posts" v-bind:key="index">
        <Post
          
          v-if="posts.length"
          :index="index"
          :postID="post._id"
          @delete_action="callDeleteAction"
          @freeze_action="callFreezeAction"
          @share_action="callShareAction"
          @edit_action="callEditAction"
          :user="user"
        />
      </div>
      <div style="width:100%; height:100%" v-if="isLoadingMore">
        <div v-for="(l,i) in [0]" :key="i" class="post-loading my-3">
          <div class="row" style="width:100%; height:100%">
            <div class="col-2">
              <general-loading :color="'#c0f0fc'"></general-loading>
            </div>
            <div class="col-10">
              <post-loading></post-loading>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NewPostEditor from "./post_editor_components/NewPostEditor";
import Post from "./post_components/Post";
import postRepository from "../../repositories/postRepository";
import DeletePostModal from "./post_components/DeletePostModal";
import SharePostModal from "./post_components/SharePostModal";
import FreezePostModal from "./post_components/FreezePostModal";
import Loading from "vue-loading-overlay";
import GeneralLoading from "../shared/GeneralLoading";
import { errorMessages } from "../shared/errorMessage";
import Logo from "../shared/assets/logo.png";
import { EventBus, SharedStorage } from "../shared/event-bus";
import PostLoading from "../shared/PostLoading";

export default {
  props: {
    lang: {
      type: String,
      default: "en"
    },
    filter: Object,
    user: Object
  },
  data() {
    return {
      isLoading: false,
      active_index: 0,
      posts: [],
      logo: Logo,
      isFreezeAction: false,
      isLoadingMore: false,
      is_no_more_post: false
    };
  },
  components: {
    NewPostEditor,
    Post,
    DeletePostModal,
    FreezePostModal,
    SharePostModal,
    GeneralLoading,
    PostLoading
  },
  async created() {
    EventBus.$on("filter_changed", async () => {
      await this.refreshPosts();
    });
    EventBus.$on("freezeSetting_changed", async () => {
      //console.log("đã gọi frefreshPots");
      await this.refreshPosts();
    });
    this.refreshPosts();
  },
  methods: {
    callDeleteAction(e) {
      //hàm chỉ có tác dụng gọi modal xác thực
      //console.log(e);
      document.getElementById("showDeleteModalBtn").click();
      if (e != undefined && e.index != undefined) this.active_index = e.index;
    },
    callEditAction(e) {
      //console.log(e);
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      //console.log("edit in ", this.active_index);
      EventBus.$emit("edit_called", this.posts[this.active_index]._id);
      //hàm chỉ có tác dụng gọi modal xác thực
      // document.getElementById("showDeleteModalBtn").click();
      // this.active_index = e.index;
    },
    callShareAction(e) {
      //console.log(e);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showShareModalBtn").click();
    },
    callFreezeAction(e) {
      //nếu ở chế độ hiển thị nhật ký đóng băng thì action sẽ là warm_up-> ngược lại
      // this.isFreezeAction = !SharedStorage.settings.freezeSettings.showFreezeDiary;
      // //console.log(this.isFreezeAction);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showFreezeModalBtn").click();
    },
    async share_post(e) {
      //đóng modal
      document.getElementById("showShareModalBtn").click();
      try {
        let listEmail = e.emails;
        //console.log(listEmail);
        let listSuccessSent = await postRepository.share_post_to_email(
          this.posts[this.active_index]._id,
          listEmail
        );
        //console.log(listSuccessSent);
        //TODO hiển thị danh sách email đã gửi thành công
      } catch (e) {
        //console.log(e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      }
    },
    async updateShareLinkStatus(e) {
      try {
        // this.isLoading = true;
        //console.log("status", e.status);
        await postRepository.updatePostShareLinkStatus(
          this.posts[this.active_index]._id,
          e.status.checked
        );
        this.posts[this.active_index].shareLinkStatus = status;
      } catch (e) {
        //console.log("lỗi bật/tắt link share", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        // this.isLoading = false;
      }
    },
    async freeze_post() {
      //đóng modal
      document.getElementById("showFreezeModalBtn").click();
      try {
        this.isLoading = true;
        //console.log("Làm nóng", this.posts[this.active_index]._id);
        await postRepository.freezePost(
          this.posts[this.active_index]._id,
          !SharedStorage.settings.freezeSettings.showFreezeDiary
        );
        this.posts.splice(this.active_index, 1);
      } catch (e) {
        //console.log("lỗi đóng làm nóng niệm", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    },
    async delete_post() {
      //đóng modal
      document.getElementById("showDeleteModalBtn").click();
      //console.log("triggle delete");
      try {
        this.isLoading = true;
        await postRepository.deletePost(this.posts[this.active_index]._id);
        this.posts.splice(this.active_index, 1);
      } catch (e) {
        //console.log("lỗi xóa kỉ niệm", e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
      }
    },
    async refreshPosts() {
      try {
        //console.log(SharedStorage.settings.freezeSettings);
        this.isLoading = true;
        SharedStorage.currentFilter.includeFreezedPost =
          SharedStorage.settings.freezeSettings.showFreezeDiary;
        let result = await postRepository.getListPost(
          (new Date()).getTime(),
          SharedStorage.currentFilter
        );
        this.posts = result.posts;
        this.lastPostIndex = this.posts.length;
      } catch (e) {
        //console.log(e);
        this.$emit("showPopup", { message: errorMessages[`${e}`] });
      } finally {
        this.isLoading = false;
        this.sortPosts();
      }
    },
    async load_more_post(e) {
      if (
        e.srcElement.scrollTop ==
          e.srcElement.scrollHeight - e.srcElement.clientHeight &&
        !this.is_no_more_post &&
        !this.isLoadingMore
      ) {
        //console.log("in the end");
        try {
          //console.log(SharedStorage.settings.freezeSettings);
          this.isLoadingMore = true;
          SharedStorage.currentFilter.includeFreezedPost =
            SharedStorage.settings.freezeSettings.showFreezeDiary;
          let result = await postRepository.getListPost(
            this.posts[this.posts.length-1].createTime,
            SharedStorage.currentFilter
          );
          if (result.posts.length > 0) {
            this.posts = this.posts.concat(result.posts);
            this.sortPosts();
          } else {
            //console.log("hết sạch post");
            this.is_no_more_post = true;
          }
        } catch (e) {
          //console.log(e);
          this.$emit("showPopup", { message: errorMessages[`${e}`] });
        } finally {
          this.isLoadingMore = false;
          this.sortPosts();
        }
      }
    },
    sortPosts(){
      //console.log("sorting posts");
      this.posts.sort((posta,postb)=>{
        return postb.createTime - posta.createTime;
      })
    }    
  }
};
</script>

<style>

/* 
.scroll_body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
.scroll_body::-webkit-scrollbar-thumb {
  background: gray; 
  border-radius: 10px;
}

.scroll_body::-webkit-scrollbar-thumb:hover {
  background:red; 
} */
.body-loading {
  border-radius: 10px;
  background-color: white;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
  height: 50%;
}
.body-loadingmore {
  border-radius: 10px;
  background-color: white;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
  height: 70%;
}
</style>
