<template>
  <div class="sharing-div my-container" v-bind:style="'background-image:url('+background_image+')'">
    <div class="input-group sharing-center mborder">
      <div style="cursor:pointer" @click="navitageToHome()">
        <img class="mid-logo mb-2" src="./navigationbar/logo.png" />
        <br />
        <h4>Memo</h4>
      </div>
      <br />
      <div v-if="isLoading">
      <general-loading></general-loading>
      </div>
      <post :share_post="post" v-else :user="user" :index=-1 ></post>
    </div>
  </div>
</template>

<script>
import Post from "./body/post_components/Post";
import postRepository from "../repositories/postRepository";
import GeneralLoading from "../components/shared/GeneralLoading"
export default {
  name: "share_screen",
  async created() {
    this.isLoading=true;
    //console.log(this.$route);
    let token = this.$route.query.ref;
    if (token == undefined) {}//console.log(this.$router.push({ path: "error" }));
    else {
      try {
        this.post = await postRepository.getSharePost(token);
        this.user = this.post.user;
        //console.log("post in sharescreen ", this.post);
      } catch (error) {
        //console.log(error);
        this.$router.push({ path: "error" });
      } finally {
        this.isLoading=false;
      }
    }
  },
  data() {
    return {
      post: {},
      user: {},
      isLoading:false,
      background_image:
        "https://images.unsplash.com/photo-1471107340929-a87cd0f5b5f3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=966&q=80"
    };
  },
  components: {
    Post,
    GeneralLoading
  },
  methods:{
      navitageToHome(){
          this.$router.push({ path: "/" })
      }
  }
};
</script>

<style>
.mid-logo {
  width: 70px;
}

.my-container {
  height: 100vh;
  background-position: center; /* Center the image */
  background-repeat: repeat-y;
  background-size: cover;
  text-align: center;
}
.slogan {
  font-style: italic;
}
.sharing-center {
  background-color: rgba(255, 255, 255, 0.85);
  display: inline-block;
  position: relative;
  top: 10%;
  width: 50%;
  margin: 0 auto;
  padding: 50px;
  z-index: 999;
}
</style>