<template>
  <v-menu v-model="show" :close-on-content-click="false">
    <template v-slot:activator="{ on }">
      <v-list-item v-on="on">{{translate('custom')}}</v-list-item>
    </template>
    <v-card width="250px">
      <v-card-title>{{translate("repeat_every")}}</v-card-title>
      <v-card-text>
        <v-row justify="center" class="mx-2">
          <v-text-field
            class="mr-2"
            outlined
            dense
            type="number"
            style="width:50px"
            label
            min="1"
            step="1"
            v-model="repeat.value"
            @input="roundValue"
          ></v-text-field>
          <v-select
            outlined
            dense
            :items="types"
            item-text="title"
            item-value="val"
            style="width:100px"
            label
            v-model="repeat.type"
          ></v-select>
        </v-row>
        <div v-if="repeat.type == 'week'" class="d-flex flex-wrap">
          <button
            class="dayOfWeekBtn"
            v-for="(day,i) in weekdays"
            :key="'dayOfWeek'+i"
            :style="{'background-color': repeat.weekdays.indexOf(i) != -1 ? '#2196F3':'#E3F2FD'}"
            @click="selectWeekDay(i)"
          >{{day}}</button>
        </div>
        <!-- <div>
          Stop after
        </div> -->
      </v-card-text>
      <v-card-actions>
        <v-btn color="grey lighten-1" text @click="show=false">{{translate("cancel")}}</v-btn>
        <v-btn color="primary darken-1" text @click="update">{{translate("ok")}}</v-btn>
      </v-card-actions>
    </v-card>
  </v-menu>
</template>

<script>
import { translate, TimeConverter } from "../../shared/translator";
const timeConverter = new TimeConverter();
const today = new Date();
export default {
  computed: {
    types() {
      return [
        { title: translate("day"), val: "day" },
        { title: translate("week"), val: "week" },
        { title: translate("month"), val: "month" },
        { title: translate("year"), val: "year" }
      ];
    },
    weekdays() {
      return [
        timeConverter.weekdayName(0),
        timeConverter.weekdayName(1),
        timeConverter.weekdayName(2),
        timeConverter.weekdayName(3),
        timeConverter.weekdayName(4),
        timeConverter.weekdayName(5),
        timeConverter.weekdayName(6)
      ];
    }
  },
  data() {
    return {
      repeat: {
        type: "day",
        value: 1,
        weekdays: [today.getDay()]
      },
      show: false
    };
  },
  methods: {
    translate: translate,
    selectWeekDay(num) {
      let index = this.repeat.weekdays.indexOf(num);
      //console.log(index);
      if (index == -1) {
        this.repeat.weekdays.push(num);
      } else this.repeat.weekdays.splice(index, 1);
      //console.log(this.repeat);
    },
    roundValue() {
      this.repeat.value = Math.round(this.repeat.value);
      if (this.repeat.value == 0) this.repeat.value = 1;
    },
    update() {
      //console.log("pre emit input event", this.repeat);
      this.$emit("input", this.repeat);
      this.show = false;
    }
  }
};
</script>

<style>
.dayOfWeekBtn {
  width: 40px !important;
  height: 40px !important;
  margin: 5px 5px;
  padding: 0px 5px;
  border-radius: 100px;
  background-color: whitesmoke;
}
</style>