<template>
  <div class="my-block">
    <ul class="list-group-sm no-padding-inline">
      <li class="list-group-item">
        <span>{{translate('show_freeze_diary')}}</span>
        <SwitchElement
          :checked="freezeSettings.showFreezeDiary"
          class="float-right"
          @updated="turnFreezeDiary"
        ></SwitchElement>
        <!-- <AuthenticationPopup></AuthenticationPopup> -->
      </li>
      <li class="list-group-item">
        <span>{{translate('freeze_diary_password')}}</span>
        <SwitchElement :checked="freezeSettings.freezePassword.turnOn" class="float-right" @updated="turnFreezePassword"></SwitchElement>
        <SettingPassword v-if="is_set_freeze_password" @save="updateFreezePass"></SettingPassword>
        <!-- <AuthenticationPopup></AuthenticationPopup> -->
      </li>
    </ul>
  </div>
</template>

<script>
import SwitchElement from "./../../shared/SwitchElement";
import AuthenticationPopup from "./../../shared/AuthenticationPopup";
import SettingPassword from "../settings/SettingPassword";
import { translate } from "../../shared/translator";
import { SharedStorage } from '../../shared/event-bus';
export default {
  data() {
    return {
      is_set_freeze_password: false,
      freezeSettings:SharedStorage.settings.freezeSettings,
    };
  },
  components: {
    SwitchElement,
    AuthenticationPopup,
    SettingPassword
  },
  props: {
    lang: {
      type: String,
      default: "en"
    },
  },
  created(){
  },
  methods: {
    translate: translate,
    turnFreezeDiary(e){
        this.$emit("turnFreezeDiary",e.checked)
    },
    updateFreezePass(pass){
        this.is_set_freeze_password=false;
        this.$emit('turnOnFreezePass',pass)
    },
    turnFreezePassword(e){
        if(e.checked && !this.freezeSettings.freezePassword.turnOn){
            this.is_set_freeze_password=true;
        }
        if(!e.checked && !this.freezeSettings.freezePassword.turnOn){
            this.is_set_freeze_password=false;
        }
        if(!e.checked && this.freezeSettings.freezePassword.turnOn){
            this.$emit("turnOffFreezePass");
        }
    }
  }
};
</script>

