<template>
  <v-expansion-panels v-model="show" :flat="true" class="mborder mr-5">
    <v-expansion-panel class="mborder">
      <v-expansion-panel-header>
        <h5>{{translate("Topics")}}</h5>
      </v-expansion-panel-header>
      <v-expansion-panel-content class="mborder">
        <div v-if="this.topics.length>0" class="mr-2">
          <!-- <span>Chủ đề</span> -->
          <div v-for="(topic,i) in topics" v-bind:key="i">
            <TopicWithBackground :topic="topic" @changed="updateTopicFilter"></TopicWithBackground>
          </div>
        </div>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import TopicWithBackground from "../../shared/TopicWithBackground";
import { SharedStorage, EventBus } from "../../shared/event-bus";
import { translate } from '../../shared/translator';
export default {
  components: {
    TopicWithBackground
  },
  created() {
    EventBus.$on("setting_changed", () => {
      this.topics = SharedStorage.settings.topicSettings.topics;
    });
  },
  data() {
    return {
      show:0,
      topics: SharedStorage.settings.topicSettings.topics,
      topicChecked: []
    };
  },
  props: {
    lang: {
      type: String,
      default: "en"
    }
  },
  methods: {
    translate:translate,
    updateTopicFilter(e) {
      while(true){
        let i = this.topicChecked.indexOf(e);
        if(i>=0) this.topicChecked.splice(i,1);
        else break;
      }
      if (e.checked) {
        this.topicChecked.
        this.topicChecked.push(e.topicID);
      }
      //console.log(this.topicChecked);
      SharedStorage.currentFilter.topics = this.topicChecked;
    }
  }
};
</script>

