    <div class="video-list-container row mx-2 mb-2" v-if="videos.length && multiple">
      <div
        class="video-list-item cursor-pointer col-auto"
        :class="video.highlight && 'video-highlight'"
        v-for="(video, index) in videos"
        :key="index"
        @click="changeHighlight(index)"
      >
        <div class="centered">
          <video class="show-vid vid-responsive" :src="video.path"></video>
        </div>
