        class="col-auto video-list-item position-relative cursor-pointer display-flex justify-content-center align-items-center"
        v-if="videos.length < maxvideo"
      >
        <svg
          class="icon add-video-svg"
          xmlns="http://www.w3.org/2000/svg"
          width="512"
          height="512"
