<script>
import { forEach, findIndex, orderBy, cloneDeep } from "lodash";
import Popper from "vue-popperjs";
import "vue-popperjs/dist/css/vue-popper.css";
import { EventBus } from '../../shared/event-bus';
export default {
  name: "VideoUpload",

  props: {
    lang:{
      type:String,
      default:"en"
    },
    dragText: {
      type: String,
      default: "Kéo video vào đây (nhiều)"
    },
    browseText: {
      type: String,
      default: "(hoặc) Chọn"
    },
    primaryText: {
      type: String,
      default: "Mặc định"
    },
    markIsPrimaryText: {
      type: String,
      default: "Đặt làm mặc định"
    },
    popupText: {
      type: String,
      default: "video này sẽ được hiển thị làm mặc định"
    },
    dropText: {
      type: String,
      default: "Thả tệp của bạn ở đây ..."
    },
    accept: {
      type: String,
      default: "video/mp4"
    },
    datavideos: {
      type: Array,
      default: () => {
        return [];
      }
    },
    multiple: {
      type: Boolean,
      default: true
    },
    showPrimary: {
      type: Boolean,
      default: true
    },
    maxvideo: {
      type: Number,
      default: 5
    },
    idUpload: {
      type: String,
      default: "video-upload"
    },
    idEdit: {
      type: String,
      default: "video-edit"
    },
    videos: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      currentIndexVideo: 0,
      isDragover: false,
      showLightbox: false,
      arrLightBox: [],
      myfiles: []
    };
  },
  components: {
    Popper
    // VuevideoLightboxCarousel
  },
  computed: {
    videoPreview() {
      let index = findIndex(this.videos, { highlight: 1 });
      if (index > -1) {
        return this.videos[index].path;
      } else {
        return this.videos.length ? this.videos[0].path : "";
      }
    },
    videoDefault() {
      if (this.videos[this.currentIndexVideo]) {
        return this.videos[this.currentIndexVideo].default;
      }
    }
  },
  methods: {
    onDrop(e) {
      this.isDragover = false;
      e.stopPropagation();
      e.preventDefault();
      let files = e.dataTransfer.files;
      if (!files.length) {
        return false;
      }
      if (!this.isValidNumberOfvideos(files.length)) {
        return false;
      }
      forEach(files, (value, index) => {
        this.createvideo(value);
        if (!this.multiple) {
          return false;
        }
      });
      if (document.getElementById(this.idUpload)) {
        document.getElementById(this.idUpload).value = [];
      }
    },
    onDragover() {
      this.isDragover = true;
    },
    createvideo(file) {
      if(this.isAcceptFile(file.name)){
      if (!this.videos.length) {
        this.myfiles.push(file);
        this.currentIndexVideo = 0;
      } else {
        this.myfiles.push(file);
      }
      this.$emit("videos_changed", this.myfiles);
      }
    },
    editVideo(file) {
      let reader = new FileReader();
      let formData = new FormData();
      formData.append("file", file);
      if (this.videos.length && this.videos[this.currentIndexVideo]) {
        this.myfiles[this.currentIndexVideo] = file;
      }
      reader.readAsDataURL(file);
      this.$emit("videos_changed", this.myfiles);
    },
    uploadFieldChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      //console.log(files[0]);
      if (!files.length) {
        return false;
      }
      if (!this.isValidNumberOfvideos(files.length)) {
        return false;
      }
      forEach(files, (value, index) => {
        this.createvideo(value);
      });
      if (document.getElementById(this.idUpload)) {
        document.getElementById(this.idUpload).value = [];
      }
    },
    editFieldChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        return false;
      }
      if (!this.isValidNumberOfvideos(files.length)) {
        return false;
      }
      forEach(files, (value, index) => {
        this.editVideo(value);
      });
      if (document.getElementById(this.idEdit)) {
        document.getElementById(this.idEdit).value = "";
      }
    },
    deletevideo(currentIndex) {
      let index_in_myfile =
        currentIndex - (this.videos.length - this.myfiles.length);
      // this.currentIndexImage=0;
      // nếu là file mới thì xóa bỏ, nếu là file cũ thì thôi
      if (index_in_myfile >= 0) this.myfiles.splice(index_in_myfile, 1);
      this.$emit("deleteVideo", currentIndex);
    },
    isValidNumberOfvideos(amount) {
      if (amount > this.maxvideo) {
        this.$emit("limit-exceeded", {amount:amount,max:this.maxvideo,type:'video'});
        return false;
      } else {
        return true;
      }
    },
    changeHighlight(currentIndex) {
      this.currentIndexVideo = currentIndex;
      // let arr = this.videos;
      // this.videos = [];
      // arr.map((item, index) => {
      //   if (currentIndex === index) {
      //     item.highlight = 1;
      //   } else {
      //     item.highlight = 0;
      //   }
      //   return item;
      // });
      // this.videos = arr;
      this.$emit("changeHighLight",currentIndex)
    },
     isAcceptFile(filename) {
      var _validFileExtensions = [".mp4"];
      for (var j = 0; j < _validFileExtensions.length; j++) {
        var sCurExtension = _validFileExtensions[j];
        if (
          filename
            .substr(
              filename.length - sCurExtension.length,
              sCurExtension.length
            )
            .toLowerCase() == sCurExtension.toLowerCase()
        ) {
          return true;
        }
      }
      return false;
    }
  },
  watch: {
    datavideos: {
      handler: function(newVal) {
        this.videos = newVal;
      },
      deep: true
    }
  },
  mounted() {
    [
      "drag",
      "dragstart",
      "dragend",
      "dragover",
      "dragenter",
      "dragleave",
      "drop"
    ].forEach(event => {
      window.addEventListener(event, e => {
        e.preventDefault();
        e.stopPropagation();
      });
    });
    document.body.addEventListener("dragleave", event => {
      event.stopPropagation();
      event.preventDefault();
      this.isDragover = false;
    });
  },
  created() {
    // this.videos = [];
    this.myfiles = [];
    EventBus.$on('add_video_via_drop',(file)=>{
      this.createvideo(file);
    })
  }
};
</script>

