<template>
  <v-menu v-model="show" offset-y absolute>
    <template v-slot:activator="{ on }">
      <v-list-item dense :two-line="repeat!=null">
        <v-list-item-avatar>
          <v-icon
            v-text="'mdi-calendar-sync-outline'"
            :class="{'purple white--text': (repeat!=null)}"
          ></v-icon>
        </v-list-item-avatar>
        <v-list-item-content v-on="on">
          <v-list-item-title>{{translate("repeat")}}</v-list-item-title>
          <v-list-item-subtitle v-if="repeat!=null">{{repeatConverted}}</v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action v-show="repeat!=null">
          <v-btn icon @click="update(null)">
            <v-icon v-text="'mdi-close'"></v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </template>
    <v-card>
      <v-list>
        <v-list-item
          v-for="(op,i) in options"
          :key="i"
          @click="update({value:op.value,type:op.type})"
        >
          <v-list-item-content>{{op.title}}</v-list-item-content>
        </v-list-item>
      </v-list>
      <RepeatTypePicker @input="update" v-model="repeat" />
    </v-card>
  </v-menu>
</template>

<script>
import { translate, TimeConverter } from "../../shared/translator";
import { SharedStorage, EventBus } from "../../shared/event-bus";
import RepeatTypePicker from "./RepeatTypePicker";
const timer = new TimeConverter();
export default {
  beforeCreate() {
    EventBus.$on("showTaskEditor", task => {
      //console.log("repeat option task update", task);
      this.repeat = task.repeatSetting;
    });
  },
  methods: {
    translate: translate,
    update(value) {
      if (value != null) {
        let today = new Date();
        value.weekdays = [today.getDay()];
      }
      //console.log(value);
      this.repeat = value;
      this.show=false;
      this.$emit("input", this.repeat);
    }
  },
  computed: {
    repeatConverted() {
      //console.log("repeat option computed", this.repeat);
      let str = `${translate("every")} ${this.repeat.value} ${translate(
        this.repeat.type
      )}`;
      if (this.repeat.type == "week") {
        let x = this.repeat.weekdays.sort();
        str += " (";
        x.forEach(element => {
          str += " " + timer.weekdayName(element);
        });
        str += ")";
      }
      return str;
    }
  },
  data() {
    return {
      show: false,
      repeat: null,
      options: [
        {
          title: translate("everyday"),
          value: 1,
          type: "day"
        },
        {
          title: translate("everyweek"),
          value: 1,
          type: "week"
        },
        {
          title: translate("everymonth"),
          value: 1,
          type: "month"
        }
      ]
    };
  },
  components: {
    RepeatTypePicker
  }
};
</script>

