import Vue from 'vue'
import Router from 'vue-router'
import App from './App.vue'
import babelPolyfill from 'babel-polyfill'
import VueAxios from 'vue-axios'
import VueSocialauth from 'vue-social-auth'
import axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import TextareaAutosize from 'vue-textarea-autosize'
import VueRecord from '@codekraft-studio/vue-record'
import AppRouter from './AppRouter.vue'
import ShareScreen from './components/ShareScreen.vue'
import AppTest from './AppTest.vue'
import ErrorScreen from './components/ErrorScreen.vue'
import DateRangePicker from 'vue-tql-daterangepicker'
import VModal from 'vue-js-modal'
import Loading from "vue-loading-overlay";
import 'vue-tql-daterangepicker/dist/vue-tql-daterangepicker.css'
import { LoaderPlugin } from 'vue-google-login';
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import DatetimePicker from 'vuetify-datetime-picker'
import LoginScreen from './components/LoginScreen'
// (Optional) import 'vuetify-datetime-picker/src/stylus/main.styl'

Vue.use(DatetimePicker)
Vue.use(Vuetify)
Vue.use(LoaderPlugin, {
  client_id: "571858267778-fa8fu87hpjoel1svuevnpjm94s4oeev2.apps.googleusercontent.com",
  client_secret: "AybaKEwNFbTVIXchlvSPRqHv"
});
Vue.use(VModal, { dynamic: true, injectModalsContainer: true })
Vue.use(Loading)
Vue.use(DateRangePicker)
Vue.use(Router)
Vue.component('icon', FontAwesomeIcon)
var router = new Router({
  routes: [
    {
      path: '/share',
      name: 'Share',
      component: ShareScreen
    },
    {
      path: '/',
      name: 'Memo',
      component: App
      // component: AppTest
    },
    {
      path: '/login',
      name: 'Login',
      component: LoginScreen
      // component: AppTest
    },
    {
      path: '/error',
      name: 'NOTFOUND',
      component: ErrorScreen
    },
    {
      path: '*',
      redirect: '/error'
    },
  ]
})
Vue.config.productionTip = false
new Vue({
  el: '#app',
  vuetify: new Vuetify(),
  router,
  render: h => h(AppRouter)
})
Vue.use(VueRecord)
Vue.use(VueAxios, axios)
Vue.use(babelPolyfill)
Vue.use(TextareaAutosize)