<template>
  <div class="accordion" id="timeline">
    <h4>{{translate("Timeline")}}</h4>
    <hr />
    <date-range-picker panel="range" :locale="lang" @update="update" @reset="reset" :submitTitle="submitTitle"/>
  </div>
</template>

<script>
import Datepicker from "vuejs-datepicker";
import CollapseButton from "./../shared/CollapseButton";
import { en, vi } from "vuejs-datepicker/dist/locale";
import { translate } from "../shared/translator";
import { SharedStorage, EventBus } from '../shared/event-bus';
export default {
  name: "calendar",
  data() {
    return {
      lang: SharedStorage.language,
      submitTitle:translate('Apply')
    };
  },
  methods: {
    translate:translate,
    update(e){
      //console.log(e);
      SharedStorage.currentFilter.time=e;
      EventBus.$emit('filter_changed');
      // this.$emit('time_filter_changed',e);
    },
    reset(){
      SharedStorage.currentFilter.time=undefined,
      EventBus.$emit('filter_changed');
    }
  },
  // props:{
  //   lang:{
  //     type:String,
  //     default:"en"
  //   }
  // },
  components: { Datepicker, CollapseButton }
};
</script>

<style>
.badge {
  color: white;
  background-color: red;
}
.accordion {
  border-radius: 10px;
  background-color: whitesmoke;
  padding-left: 20px;
  /* margin-right: 20px; */
  /* width: 50%; */
  height: 90vh;
}
.date-picker {
  border-radius: 12px !important;
}
.datepicker_custom{
  border-radius: 12px !important;
  z-index: 99999;
}
</style>
