<template>
  <v-col cols="3">
  <div class="scroll_extensions px-3">
    <div v-if="isLoading" class="extention-loading mborder">
      <general-loading class="float-right mr-5" :color="'#c0f0fc'"></general-loading>
    </div>
    <!-- <button
      v-else
      @click="updateSetting"
      class="btn btn-primary btn-function float-right"
    >{{ translate("save_change") }}</button> -->
    <h4 class="mt-1">{{translate("Settings")}}</h4>
    <hr class="mr-3" />
    <AuthenticationPopup
      :show="asking_password"
      title="setting_password_required"
      :password="settingPassword"
      @correct="handleCorrect"
      @incorrect="handleIncorrect"
      caller_id="setting_tab"
    ></AuthenticationPopup>
    <CollapseButton :name="device_str" icon="laptop-code" :show_icon="true"></CollapseButton>
    <DevicesSettingElement
      @loading="$emit('loading')"
      @logout="$emit('logout')"
      class="collapse"
      id="id_laptop-code_element"
      :list_devices="user.devices"
      :ip="user.ip"
    ></DevicesSettingElement>
    <!-- <CollapseButton :name="connect_str" icon="link" :show_icon="true"></CollapseButton>
    <ConnectsSettingElement  class="collapse" id="id_link_element"></ConnectsSettingElement>-->
    <CollapseButton :name="notify_str" icon="bell" :show_icon="true"></CollapseButton>
    <NotificationSettingElement
      v-if="settings.notificationSettings != undefined"
      class="collapse"
      id="id_bell_element"
      @update="updateNotificationSettings"
      :settings="settings.notificationSettings"
      :userTopic="settings.topicSettings.topics"
    ></NotificationSettingElement>
    <CollapseButton :name="security_str" icon="user-shield" :show_icon="true"></CollapseButton>
    <SecuritySettingElement
      class="collapse"
      id="id_user-shield_element"
      @turnOffSettingPass="turnOffSettingPass"
      @turnOffAppPass="turnOffAppPass"
      :securitySettings="settings.securitySettings"
      @update="updateSecuritySettings"
    ></SecuritySettingElement>
    <CollapseButton :name="freeze_post_str" icon="snowflake" :show_icon="true"></CollapseButton>
    <FreezePostSettingElement
      :freezeSettings="settings.freezeSettings"
      class="collapse"
      id="id_snowflake_element"
      @turnFreezeDiary="turnFreezeDiary"
      @turnOffFreezePass="turnOffFreezePass"
      @turnOnFreezePass="turnOnFreezePass"
    ></FreezePostSettingElement>
    <CollapseButton :name="topic_str" icon="tags" :show_icon="true"></CollapseButton>
    <TopicSettingElement
      v-if="settings.topicSettings != undefined"
      class="collapse"
      id="id_tags_element"
      :topicSettings="settings.topicSettings.topics"
      @update="updateTopicSettings"
    ></TopicSettingElement>
    <CollapseButton
      class="dropdown-toggle mb-5"
      id="dropdownLangButton"
      data-toggle="dropdown"
      :name="lang_str"
      icon="language"
      :show_icon="true"
    ></CollapseButton>
    <div class="dropdown-menu dropdown-menu-right">
      <a v-for="(lang,i) in langs" v-bind:key="i" class="dropdown-item" @click="changeLanguage(i)">
        {{ lang.label }}
      </a>
    </div>
  </div>
  </v-col>
</template>

<script>
import CollapseButton from "./../../shared/CollapseButton";
import ConnectsSettingElement from "./ConnectsSettingElement";
import DevicesSettingElement from "./DevicesSettingElement";
import SecuritySettingElement from "./SecuritySettingElement";
import NotificationSettingElement from "./NotificationSettingElement";
import FreezePostSettingElement from "./FreezePostSettingElement";
import AuthenticationPopup from "../../shared/AuthenticationPopup";
import userRepository from "../../../repositories/userRepository";
import TopicSettingElement from "./TopicSettingElement";
import { errorMessages } from "../../shared/errorMessage";
import { translate } from "../../shared/translator";
import GeneralLoading from "../../shared/GeneralLoading";
import { EventBus, SharedStorage } from "../../shared/event-bus";

export default {
  components: {
    CollapseButton,
    ConnectsSettingElement,
    DevicesSettingElement,
    SecuritySettingElement,
    NotificationSettingElement,
    FreezePostSettingElement,
    AuthenticationPopup,
    TopicSettingElement,
    GeneralLoading
  },
  data() {
    return {
      isLoading: false,
      freezeChange: false,
      user: SharedStorage.currentUser,
      userSettings: SharedStorage.settings,
      settingPassword: "",
      asking_password: false,
      requiringTurnOffAppPass: false,
      requiringTurnOffSettingPass: false,
      requiringTurnOffFreezePass: false,
      device_str: translate("Devices"),
      connect_str: translate("Connects"),
      security_str: translate("Security"),
      notify_str: translate("Notifications"),
      freeze_post_str: translate("Freeze_Diary"),
      topic_str: translate("Topics"),
      lang_str: translate("Language"),
      settings: SharedStorage.settings,
      langs: [
        { label: "English", locale: "en" },
        { label: "Tiếng Việt", locale: "vi" }
      ],
      wrong_pass_count: 0
    };
  },
  watch: {
    // userSettings: function(val) {
    //   //console.log("update setting");
    //   this.settings = val;
    // }
  },
  beforeMount() {
    this.asking_password = this.settings.securitySettings.settingPassword.turnOn;
    this.settingPassword = this.settings.securitySettings.settingPassword.password;
  },
  beforeCreate() {
    this.user = SharedStorage.currentUser;
    this.userSettings = SharedStorage.settings;
  },
  methods: {
    translate: translate,
    updateNotificationSettings(setting) {
      this.settings.notificationSettings = setting;
      this.updateSetting();
    },
    turnOnFreezePass(pass) {
      //console.log(pass);
      this.settings.freezeSettings.freezePassword.turnOn = true;
      this.settings.freezeSettings.freezePassword.password = pass;
      this.updateSetting();
    },
    turnOffFreezePass() {
      if (this.settings.securitySettings.settingPassword.turnOn) {
        this.asking_password = true;
        this.requiringTurnOffFreezePass = true;
      } else {
        this.settings.freezeSettings.freezePassword.turnOn = false;
        this.settings.freezeSettings.freezePassword.password = "";
      }
      this.updateSetting();
    },
    turnFreezeDiary(isOn) {
      this.settings.freezeSettings.showFreezeDiary = isOn;
      this.freezeChange = true;
      this.updateSetting();
    },
    updateSecuritySettings(setting) {
      this.settings.securitySettings = setting;
      this.updateSetting();
    },
    updateTopicSettings(setting) {
      this.settings.topicSettings = setting;
      this.updateSetting();
    },
    changeLanguage(i) {
      EventBus.$emit("change_language", this.langs[i].locale);
    },
    handleCorrect() {
      this.asking_password = false;
      if (this.requiringTurnOffAppPass) {
        this.settings.securitySettings.appPassword.turnOn = false;
        this.settings.securitySettings.appPassword.password = "";
        this.requiringTurnOffAppPass = false;
      }
      if (this.requiringTurnOffSettingPass) {
        this.settings.securitySettings.settingPassword.turnOn = false;
        this.settings.securitySettings.settingPassword.password = "";
        this.requiringTurnOffSettingPass = false;
      }
      if (this.requiringTurnOffFreezePass) {
        this.settings.freezeSettings.freezePassword.turnOn = false;
        this.settings.freezeSettings.freezePassword.password = "";
        this.requiringTurnOffFreezePass = false;
      }
    },
    handleIncorrect() {
      this.wrong_pass_count++;
      if (this.wrong_pass_count > 4) {
        this.wrong_pass_count = 0;
        EventBus.$emit("force_logout");
        EventBus.$emit("showError", errorMessages[401]);
      }
    },
    turnOffAppPass() {
      if (this.settings.securitySettings.settingPassword.turnOn) {
        this.asking_password = true;
        this.requiringTurnOffAppPass = true;
      } else {
        this.settings.securitySettings.appPassword.turnOn = false;
        this.settings.securitySettings.appPassword.password = "";
      }
      this.updateSetting();
    },
    turnOffSettingPass() {
      if (this.settings.securitySettings.settingPassword.turnOn) {
        this.asking_password = true;
        this.requiringTurnOffSettingPass = true;
      } else {
        this.settings.securitySettings.settingPassword.turnOn = false;
        this.settings.securitySettings.settingPassword.password = "";
      }
      this.updateSetting();
    },
    async updateSetting() {
      try {
        //console.log(this.settings);
        
        this.isLoading = true;
        await userRepository.updateUserSettings(this.settings);
        if (this.freezeChange) {
          EventBus.$emit("freezeSetting_changed");
          this.freezeChange = false;
        }
        EventBus.$emit("setting_changed");
        //console.log("update xong");
      } catch (e) {
        //console.log(e);
        //TODO thông báo update thất bại
        // let message = errorMessages[`${e}`];
        EventBus.$emit("showError", errorMessages[`${e}`]);
        // this.$emit("showPopup", { message: message });
      } finally {
        this.isLoading = false;
      }
    }
  }
};
</script>

<style>
/* .setting-tab {
  background-color: white;
  border-radius: 10px;
  margin-right: 10px
} */
.row::after{
  display: none;
}
</style>
