<template>
  <div class="d-flex">
    <v-text-field
      v-if="loading"
      loading
      disabled
      type="text"
      v-model="task.taskContent"
      :prepend-icon="'mdi-circle-outline'"
    ></v-text-field>
    <v-text-field
      v-else
      v-model="task.taskContent"
      :prepend-icon="task.taskContent==null ? 'mdi-plus' : 'mdi-circle-outline'"
      clear-icon="mdi-close-circle"
      clearable
      type="text"
      :placeholder="translate('create_new_task')"
      @click="create_task"
      @blur="cancel"
      counter="100"
      maxlength="100"
      @keydown="checkEnterKey"
    >
      <template v-if="task.taskContent!=null && task.taskContent!=''" v-slot:append-outer>
        <v-btn text small color="primary" @click="uploadTask">{{translate('Add')}}</v-btn>
      </template>
    </v-text-field>
  </div>
</template>

<script>
import { translate } from "../../shared/translator";
import { EventBus } from "../../shared/event-bus";
import taskRepository from "../../../repositories/taskRepository";
export default {
  methods: {
    translate: translate,
    create_task() {
      if (this.task.taskContent == null) this.task.taskContent = "";
      EventBus.$emit("showTaskEditor", this.task);
    },
    async checkEnterKey(e) {
      if (event.key == "Enter" && this.task.taskContent.trim() != "") {
        //console.log(this.task.taskContent);
        await this.uploadTask();
      }
    },
    async uploadTask() {
      try {
        this.loading = true;
        //console.log("Trước khi upload", this.task);
        let newTask = await taskRepository.uploadTask(this.task);
        //console.log("Sau khi upload", newTask);

        this.$emit("onNewTask", newTask);
        this.task.taskContent = null;
      } catch (error) {
        //TODO thông báo không tạo được task
      } finally {
        this.loading = false;
      }
    },
    cancel() {
      if (this.task.taskContent == "") this.task.taskContent = null;
    }
  },
  data() {
    return {
      task: { taskContent: null },
      loading: false
    };
  }
};
</script>

