<template>
  <div class="d-flex" v-if="deadline && deadline!=''" >
    <v-icon v-text="'mdi-calendar-clock'" color="red darken-3" small></v-icon>
    <p class="taskOptionText mx-1">{{deadlineString}}</p>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faBusinessTime, faHome } from "@fortawesome/free-solid-svg-icons";
import { translate, TimeConverter } from "../../shared/translator";
import { SharedStorage } from "../../shared/event-bus";
library.add(faBusinessTime, faHome);
export default {
  mounted(){
    //console.log(this.deadline);
  },
  computed: {
    deadlineString() {
      return this.deadline != undefined
        ? new TimeConverter().convertTimeFromNow(this.deadline)
        : translate("Deadline");
    }
  },
  data() {
    return {};
  },
  props: {
    deadline: {
      type: String| Date,
      default: null
    }
  }
};
</script>

<style>
.deadlineIcon {
  color: blue;
}
</style>