<template>
    <!-- Button trigger modal -->

    <!-- Modal -->
    <div
      class="modal fade mborder"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="deleteModalTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <icon icon="trash" size="lg" class="float-left mx-3 mt-1"></icon>
            <h5 class="modal-title" id="deleteModalTitle">
              {{ delete_modal_title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{ delete_modal_body }}
          </div>
          <div class="modal-footer">
            <button @click="$emit('freeze')" type="button" class="btn btn-secondary mborder">
              {{ freeze_btn_str }}
            </button>
            <button @click="$emit('edit')" type="button" class="btn btn-secondary mborder">
              {{ edit_btn_str }}
            </button>
            <button @click="$emit('delete')" type="button" class="btn btn-primary mborder">
              {{ delete_btn_str }}
            </button>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { translate } from '../../shared/translator';
export default {
  props:{
    lang:{
      type:String,
      default:"en"
    },
  },
  data() {
    return {
      delete_modal_title:translate('delete_modal_title'),
      delete_modal_body:translate('delete_modal_body'),
        delete_btn_str:translate("Delete"),
        freeze_btn_str:translate("Freeze"),
        edit_btn_str:translate("Edit")
    };
  }
};
</script>

<style></style>
