<template>
  <div class="my-block">
    <ul class="list-group-sm no-padding-inline">
      <li class="list-group-item">
        <span>{{translate('password_app')}}</span>
        <SwitchElement
          :checked="securitySettings.appPassword.turnOn"
          class="float-right"
          @updated="protect_app"
        ></SwitchElement>
        <SettingPassword v-if="is_set_app_password" @save="updateAppPass"></SettingPassword>
      </li>
      <li class="list-group-item">
        <span>{{translate('password_setting')}}</span>
        <SwitchElement
          :checked="securitySettings.settingPassword.turnOn"
          class="float-right"
          @updated="protect_setting"
        ></SwitchElement>
        <SettingPassword v-if="is_set_setting_password" @save="updateSettingPass"></SettingPassword>
      </li>
      <li class="list-group-item">
        <a href>{{translate('change_password')}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
import SwitchElement from "../../shared/SwitchElement";
import { translate } from "../../shared/translator";
import SettingPassword from "./SettingPassword";
export default {
  components: { SwitchElement, SettingPassword },
  methods: {
    translate: translate,
    protect_app(e) {
      if (this.securitySettings.appPassword.turnOn && !e.checked) {
        this.$emit("turnOffAppPass");
      }
      if (!this.securitySettings.appPassword.turnOn && e.checked) {
        this.is_set_app_password = true;
      }
      if (!this.securitySettings.appPassword.turnOn && !e.checked) {
        this.is_set_app_password = false;
      }
    },
    protect_setting(e) {
      if (this.securitySettings.settingPassword.turnOn && !e.checked) {
        this.$emit("turnOffSettingPass");
      }
      if (!this.securitySettings.settingPassword.turnOn && e.checked) {
        this.is_set_setting_password = true;
      }
      if (!this.securitySettings.settingPassword.turnOn && !e.checked) {
        this.is_set_setting_password = false;
      }
    },
    updateSettingPass(pass) {
      let newSecureSetting = this.securitySettings;
      newSecureSetting.settingPassword.password = pass;
      newSecureSetting.settingPassword.turnOn = true;
      this.is_set_setting_password=false;
      this.$emit("update",newSecureSetting)
    },
    updateAppPass(pass) {
      let newSecureSetting = this.securitySettings;
      newSecureSetting.appPassword.password = pass;
      newSecureSetting.appPassword.turnOn = true;
      this.is_set_app_password=false;
      this.$emit("update",newSecureSetting)
    }
  },
  data() {
    return {
      is_set_app_password: false,
      is_set_setting_password: false
    };
  },
  props: {
    lang: {
      type: String,
      default: "en"
    },
    checked: Boolean,
    securitySettings: {
      type: Object,
      default: {
        appPassword: {
          turnOn: false,
          password: "ádfadsafsdfasd"
        },
        settingPassword: {
          turnOn: false,
          password: "ádfasdfasdfsad"
        }
      }
    }
  }
};
</script>

<style>
.password-div {
  border: 1px dashed #a8a8a8;
  border-radius: 10px;
  background-color: #fff;
  margin: 10px;
  padding: 20px;
}
.wrong_pass {
  border: solid red 1px !important;
}
</style>
