<template>
  <div class="scroll_body">
    <TaskCreator @onNewTask="onNewTask" />
    <v-simple-table>
      <template v-slot:default>
        <tbody>
          <Task
            @onTaskChanged="onTaskChanged"
            v-for="task in tasks"
            :key="task._id"
            :task="task"
            @click="showEditor(task)"
          />
        </tbody>
      </template>
    </v-simple-table>
    <div v-if="isLoading">
      <general-loading :color="'#c0f0fc'"></general-loading>
    </div>
  </div>
</template>

<script>
import Task from "./task_components/Task";
import taskRepository from "../../repositories/taskRepository";
import GeneralLoading from "../shared/GeneralLoading";
import TaskCreator from "./task_components/TaskCreator";
import { EventBus } from "../shared/event-bus";
import { translate, TimeConverter } from "../shared/translator";
import { faBorderAll, faHourglassEnd } from "@fortawesome/free-solid-svg-icons";
const timer = new TimeConverter();
export default {
  async created() {
    EventBus.$on("taskViewChanged", view => {
      switch (view) {
        case "today":
          this.tasks = this.todayTasks;
          break;
        case "important":
          this.tasks = this.importantTasks;
          break;
        case "task_passed":
          this.tasks = this.passedTasks;
          break;
        case "future":
          this.tasks = this.futureTasks;
          break;
        case "completed":
          this.tasks = this.completedTasks;
          break;
        default:
          break;
      }
      EventBus.$on("onTaskDeleted", task => {
        //console.log("deletedTask", task);
        this.findAndDeleteExitsTask(task);
        this.updateCount();
      });
      //console.log(this.tasks);
    });
    this.tasks = [];
    this.todayTasks = [];
    this.passedTasks = [];
    this.importantTasks = [];
    this.futureTasks = [];
    this.completedTasks = [];
    this.loadTask();
    this.tasks = this.todayTasks;
  },
  methods: {
    translate: translate,
    onTaskChanged(obj) {
      obj.tasks.forEach(element => {
        this.addTaskToView(element);
      });
      this.sortTasks();
    },
    onNewTask(task){
      this.addTaskToView(task);
      this.sortTasks();
    },
    async loadTask() {
      try {
        this.isLoading = true;
        let start = this.tasks.length;
        let list = await taskRepository.getListTask(start);
        //console.log("danh sach task:", list);
        this.completedTasks = list.completedTasks;
        //console.log(this.completedTasks);
        list.tasks.forEach(element => {
          this.addTaskToView(element);
        });
        this.sortTasks();
      } catch (e) {
      } finally {
        this.isLoading = false;
      }
    },
    addTaskToView(task) {
      this.findAndDeleteExitsTask(task);
      if (task.done) this.completedTasks.push(task);
      else {
        let today = timer.today(0, 0);
        let tomorrow = timer.tomorow(0, 0);
        if (task.star) this.importantTasks.push(task);
        if (typeof task.deadline == "number") {
          if (task.deadline > tomorrow) this.futureTasks.push(task);
          else if (task.deadline < today) this.passedTasks.push(task);
          else this.todayTasks.push(task);
        } else {
          this.todayTasks.push(task);
        }
      }
    },
    findAndDeleteExitsTask(task) {
      let index;
      const isSameTask = taskInArray => taskInArray._id == task._id;
      index = this.todayTasks.findIndex(isSameTask);
      if (index >= 0) this.todayTasks.splice(index, 1);
      index = this.futureTasks.findIndex(isSameTask);
      if (index >= 0) this.futureTasks.splice(index, 1);
      index = this.passedTasks.findIndex(isSameTask);
      if (index >= 0) this.passedTasks.splice(index, 1);
      index = this.completedTasks.findIndex(isSameTask);
      if (index >= 0) this.completedTasks.splice(index, 1);
      index = this.importantTasks.findIndex(isSameTask);
      if (index >= 0) this.importantTasks.splice(index, 1);
    },
    sortTasks() {
      this.futureTasks.sort(this.compareTaskFunction);
      //console.log("futureTask:", this.futureTasks);
      this.todayTasks.sort(this.compareTaskFunction);
      //console.log("todayTask:", this.todayTasks);
      this.passedTasks.sort(this.compareTaskFunction);
      //console.log("passedTask:", this.passedTasks);
      this.completedTasks.sort(this.compareTaskFunction);
      //console.log("completedTask:", this.completedTasks);
      this.importantTasks.sort(this.compareTaskFunction);
      //console.log("importantTask:", this.importantTasks);
      this.updateCount();
    },
    updateCount() {
      EventBus.$emit("taskCountChanged", {
        today: this.todayTasks.length,
        future: this.futureTasks.length,
        passed: this.passedTasks.length,
        completed: this.completedTasks.length,
        important: this.importantTasks.length
      });
    },
    compareTaskFunction(task1, task2) {
      let d1 =
        typeof task1.deadline == "number" ? task1.deadline : 999999999999999;
      let d2 =
        typeof task2.deadline == "number" ? task2.deadline : 999999999999999;
      let now = new Date();
      let p1 = d1 - now.getTime() + task1.star ? 9999999 : 0;
      let p2 = d2 - now.getTime() + task2.star ? 9999999 : 0;
      return p1 - p2;
    }
  },
  components: {
    Task,
    GeneralLoading,
    TaskCreator
  },

  data() {
    return {
      newTaskContent: null,
      tasks: [],
      todayTasks: [],
      passedTasks: [],
      importantTasks: [],
      futureTasks: [],
      completedTasks: [],
      collections: {
        today: [],
        passed: [],
        important: [],
        future: []
      },
      isLoading: true
    };
  }
};
</script>

