<template>
  <v-menu v-model="show" offset-y absolute>
    <template v-slot:activator="{ on }">
      <v-list-item :two-line="date!=null">
        <v-list-item-avatar>
          <v-icon v-text="'mdi-calendar-clock'" :class="{'red darken-3 white--text': (date!=null)}"></v-icon>
        </v-list-item-avatar>
        <v-list-item-content v-on="on">
          <v-list-item-title>{{translate("deadline")}}</v-list-item-title>
          <v-list-item-subtitle v-if="date!=null">{{dateConverted}}</v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action v-show="date!=null">
          <v-btn icon @click="update(null)">
            <v-icon v-text="'mdi-close'"></v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </template>
    <v-card>
      <v-list>
        <v-list-item v-for="(op,i) in options" :key="i" @click="update(op.value)">
          <v-list-item-content>{{op.title}}</v-list-item-content>
        </v-list-item>
      </v-list>
      <DateTimePicker @input="update" v-model="date" class="mx-2" />
    </v-card>
  </v-menu>
</template>

<script>
import { translate, TimeConverter } from "../../shared/translator";
import { SharedStorage, EventBus } from "../../shared/event-bus";
import DateTimePicker from "./DateTimePicker";
const timer = new TimeConverter();
export default {
  beforeCreate() {
    EventBus.$on("showTaskEditor", task => {
      //console.log("deadline option task update", task);
      if(task.deadline !=null)
      this.date = new Date(task.deadline);
      else this.date = null;
    });
  },
  methods: {
    translate: translate,
    update(time) {
      //console.log("date time update", time);
      this.date = time;
      this.$emit("input", time);
      this.show = false;
    }
  },
  computed: {
    dateConverted() {
      return timer.convertTimeFromNow(this.date);
    }
  },
  data() {
    return {
      show: false,
      date: null,
      items: ["Sáng 9:00", "Trưa 10:00"],
      options: [
        {
          title: timer.convertTimeFromNow(timer.today(21, 0)),
          value: timer.today(21, 0)
        },
        {
          title: timer.convertTimeFromNow(timer.tomorow(21, 0)),
          value: timer.tomorow(21, 0)
        },
        {
          title: timer.convertTimeFromNow(timer.nextweek(21, 0)),
          value: timer.nextweek(21, 0)
        }
      ]
    };
  },
  props: {
    value: {
      type: Date | String | Number,
      default: null
    }
  },
  components: {
    DateTimePicker
  }
};
</script>

