<template>
  <v-card :loading="loading" class="mr-3 mborder">
    <v-list three-line>
        <v-list-item v-for="(device, i) in devices" v-bind:key="i">
          <v-list-item-icon>
            <v-icon v-text=" device.type == 'mobile'?'mdi-cellphone':'mdi-laptop'"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ device.name }}</v-list-item-title>
            <v-list-item-subtitle v-if="device.thisDevice">{{active_device_str}}</v-list-item-subtitle>
            <v-list-item-subtitle
              v-else
            >({{ last_active_str }}{{ device.last_active }}, {{translate('at')}} :{{translate(device.location)}})</v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn icon v-on="on" @click="logoutDevice(i)">
                  <v-icon v-text="'mdi-logout'"></v-icon>
                </v-btn>
              </template>
              <span>{{translate('Logout')}}</span>
            </v-tooltip>
          </v-list-item-action>
        </v-list-item>
    </v-list>
  </v-card>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faTabletAlt,
  faDesktop,
  faSignOutAlt
} from "@fortawesome/free-solid-svg-icons";
import serviceRepository from "../../../repositories/serviceRepository";
import userRepository from "../../../repositories/userRepository";
import { translate } from "../../shared/translator";
library.add(faTabletAlt, faDesktop, faSignOutAlt);
export default {
  async created() {
    //console.log(this.ip);
    this.devices = [];
    for (let i = 0; i < this.list_devices.length; i++) {
      let de = this.list_devices[i];
      let type = de.info.search("Android") >= 0 ? "mobile" : "desktop";
      let last_active = new Date(de.lastAccess).toLocaleString(this.lang);
      //console.log("location: ", location);
      this.devices.push({
        name: de.info,
        type: type,
        last_active: last_active,
        thisDevice: de.ip == this.ip,
        location: de.location
      });
    }
  },
  data() {
    return {
      logout_str: translate("Logout", this.lang),
      last_active_str: translate("last_active", this.lang),
      active_device_str: translate("this_device", this.lang),
      devices: [],
      loading:false
    };
  },
  props: {
    list_devices: Array,
    ip: String
  },
  methods: {
    translate: translate,
    async logoutDevice(index) {
      this.loading=true;
      //console.log("index ", this.list_devices[index].ip);
      try {
        let result = await userRepository.logoutDevice(
          this.list_devices[index].ip
        );
        if (this.devices[index].thisDevice) this.$emit("logout");
        this.devices.splice(index, 1);
      } catch (error) {
        window.alert("Lỗi đăng xuất khỏi thiết bị, vui lòng thử lại");
      } finally {
        this.loading=false;
      }
    }
  }
};
</script>

<style>
.list-group-item .devicesetting {
  border: none;
  margin-bottom: 0;
}
</style>
