library.add( faGithub,faGoogle,faTwitter,faFacebook,faInstagram,faSoundcloud);
import { translate } from "../../shared/translator";
export default {
  props:{
    lang:{
      type:String,
      default:"en"
    },
  },
  data() {
    return {
      socials:[
        {
          provider:'Google',
          icon:'google',
