          provider:'Soundcloud',
          icon:'soundcloud',
          status:'disconected',
          user:'tranquanglinh.pt@gmail.com'
        },
      ]
    };
  },
  methods: {
    translate:translate,
    AuthProvider(provider) {
      var self = this;

      this.$auth
        .authenticate(provider)
        .then(response => {
          //console.log("resssss", response);
          self.SocialLogin(provider, response);
        })
        .catch(err => {
          //console.log({ err: err });
        });
    },

