<template>
  <v-col cols="3">
    <div>
      <div v-if="isLoading" class="extention-loading mborder">
        <general-loading class="float-right mr-5" :color="'#c0f0fc'"></general-loading>
      </div>
      <button
        v-else
        @click="updateProfile"
        class="btn btn-primary btn-function float-right"
      >{{ translate("Update") }}</button>
      <h4>{{translate("Profile")}}</h4>
      <hr class="mr-3" />
      <span>{{ translate("avatar")}}:</span>
      <br />
      <div class="d-flex my-2 justify-content-center">
        <img v-if="file!=null" :src="avatar_source" class="avatar-profile rounded-circle" />
        <Avatar v-else :src="avatar_source" :username="user.name" :size="200" />
        <input
          @change="change_avatar"
          ref="avatar_edit"
          type="file"
          style="display:none"
          accept="image/x-png, image/gif, image/jpeg"
        />
        <button class="btn float-right" @click="$refs.avatar_edit.click()">
          <icon icon="edit" size="sm" />
        </button>
      </div>
      <div v-for="(info, index) in infos" v-bind:key="index" class="my-1">
        <div class="d-flex justify-content-between">
          <div class="mt-2">
            <span>{{ labels[index] }}</span>
          </div>
          <div class="mr-2">
            <div class="input-group input-group-info">
              <input
                :type="(index!=1)?'text':'date'"
                class="form-control input-info"
                disabled
                v-model="infos[index]"
                v-bind:id="'edit' + index"
              />
              <button class="btn input-group-append btn-edit" @click="enable_input(index)">
                <icon icon="edit" size="sm" />
              </button>
            </div>
          </div>
        </div>
      </div>
      <button class="btn btn-link px-0" @click="logout">{{translate("Logout")}}</button>
    </div>
  </v-col>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import userRepository from "../../repositories/userRepository";
import { translate } from "../shared/translator";
import { SharedStorage, EventBus } from "../shared/event-bus";
import GeneralLoading from "../shared/GeneralLoading";
import Avatar from "vue-avatar";
library.add({ faEdit });
export default {
  beforeCreate() {
    this.user = SharedStorage.currentUser;
    this.avatar_source = this.user.avatar;
    this.infos = [
      this.user.name,
      this.user.birthday,
      this.user.email,
      this.user.contact
    ];
  },
  components: {
    Avatar,
    GeneralLoading
  },
  computed: {
    avatarFromFile: () => this.avatar_source
  },
  data() {
    return {
      avatarKey: 0,
      user: SharedStorage.currentUser,
      isLoading: false,
      avatar_source: this.user.avatar,
      file: null,
      avatar_label: translate("avatar", this.lang),
      labels: [
        translate("display_name", this.lang),
        translate("birthday", this.lang),
        translate("email", this.lang),
        translate("contact", this.lang)
      ],
      infos: [
        this.user.name,
        this.user.birthday,
        this.user.email,
        this.user.contact
      ]
    };
  },
  methods: {
    translate: translate,
    logout() {
      EventBus.$emit("force_logout", "profile");
    },
    enable_input: function(index) {
      //console.log(index);
      var input = document.getElementById("edit" + index);
      input.disabled = false;
      input.select();
    },
    change_avatar(e) {
      let f = e.target.files[0] || e.dataTransfer.files[0];
      this.file = f;
      this.avatar_source = URL.createObjectURL(f);
      // //console.log(this.avatar_source);
      // this.avatarKey++;
      // this.$refs.avatar.levelUp();
    },
    async updateProfile() {
      //console.log("updating");

      let updated_profile = {
        name: this.infos[0],
        birthday: this.infos[1],
        gmail: this.infos[2],
        contact: this.infos[3]
      };
      try {
        this.isLoading = true;
        await userRepository.updateProfile(updated_profile, this.file);
      } catch (e) {
        //console.log(e);
        EventBus.$emit("showError", errorMessages[`${e}`]);
      } finally {
        this.isLoading = false;
      }
    }
  },
  props: {
    lang: {
      type: String,
      default: "en"
    }
  }
};
</script>

<style>
.avatar-profile {
  width: 200px;
  height: 200px;
  margin: auto;
  /* background-position: center center; */
  /* background-repeat: no-repeat; */
  object-fit: cover; /* Do not scale the image */
  object-position: center; /* Center the image within the element */
}
.input-info {
  background-color: white !important;
  border-radius: 32px !important;
  border: none;
}
.input-group-info {
  background-color: white;
  /* margin:20px; */
  margin-top: 5px;
  margin-right: 10px !important;

  /* padding: 5px; */
  border-radius: 10px;
}
.btn-edit {
  border-radius: 32px !important;
}
</style>
