<template>
  <div
      class="modal fade mborder my-container"
      id="infoModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="infoModalTitle"
      aria-hidden="true"
      data-keyboard="false"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content info-center mborder">
          <div class="modal-body ">
            <img :src="logo" alt="Memo" class="logo-medium-responsive ">
            <br/>
            <h5
              class="modal-title"
              id="infoModalTitle"
            >
              {{message.title}}
            </h5>
            <p>{{ message.content }}</p>
          </div>
          <div class="modal-footer">
            <button
              @click="ok_action"
              type="button"
              data-dismiss="modal"
              class="btn btn-primary mborder"
            >
              {{ ok_btn_str }}
            </button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import LOGO from "../navigationbar/logo.png"
export default {
  name:'info_popup',
  data() {
    return {
      ok_btn_str: "OK",
      logo:LOGO
    };
  },
  props: {
    show: Boolean,
    message:Object
  },
  methods:{
    ok_action(){
      if(this.message.ok_action != undefined){
        this.message.ok_action();
      this.$emit("ok_clicked",true);
      }else{
        this.$emit("ok_clicked",false)
      }
    }
  }
};

</script>

<style>
.my-error-popup {
  background-color: rgba(0, 0, 0, 0.2);
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 999;
  text-align: center;
  /* display: inline-block; */
}
.info-center{
  display: inline-block;
  position: relative;
  top: 40%;
  width: 100%;
  margin: 0 auto;
  z-index: 9990;
}
.my-btn {
  border-radius: 10px;
}
.my-border-radius {
  border-radius: 10px;
}
.wrong-input {
  border: solid 1px red !important;
}
.my-shake-animation {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}
.logo-medium-responsive{
  width: 30%;
  height: 30%;
}
.mborder{
  border-radius: 10px;
}
</style>
