<template>
  <div :key="myAppKey">
    <AuthenticationPopup
      :full_screen="true"
      :show="asking_password"
      title="app_password_required"
      :password="appPassword"
      @correct="asking_password=false"
      @incorrect="handleIncorrect"
    ></AuthenticationPopup>
    <button hidden id="showInfoModalBtn" data-toggle="modal" data-target="#infoModal"></button>
    <InfoPopup :message="popupMessage" @ok_clicked="reloadPage"></InfoPopup>
    <!-- <LoginScreen v-if="!isLoged" @logged_in="change_user" @showPopup="showPopup"></LoginScreen> -->
    <div>
      <NavigatorBar class="sticky-top" :activeTab="active_tab" @search="searchPost"></NavigatorBar>
      <v-row>
        <v-col :cols="3">
          <LeftExtension />
        </v-col>
        <v-col>
          <Body ref="body" @showPopup="showPopup"></Body>
        </v-col>
        <keep-alive>
          <component class="extensiontab mt-3" v-bind:is="currentRightComponent"></component>
        </keep-alive>
      </v-row>
    </div>
  </div>
</template>

<script>
import RightExtension from "./components/right_extensions/RightExtension";
import LeftExtension from "./components/left_extensions/LeftExtension";
import NavigatorBar from "./components/navigationbar/NavigatorBar";
import LoginScreen from "./components/LoginScreen";
import Body from "./components/body/Body.vue";
import { getCookie, setCookie } from "./repositories/cookieRepository";
import userRepository from "./repositories/userRepository";
import InfoPopup from "./components/shared/InfoPopup";
import { EventBus, SharedStorage } from "./components/shared/event-bus";
import AuthenticationPopup from "./components/shared/AuthenticationPopup";
import { errorMessages } from "./components/shared/errorMessage";
import messageRepository from "./repositories/messageRepository";
import Setting from "./components/right_extensions/settings/Setting";
import Profile from "./components/right_extensions/Profile";
import TaskEditor from "./components/right_extensions/task_editor_components/TaskEditor";
import Filters from "./components/right_extensions/filters/Filters";
export default {
  name: "app",
  data() {
    return {
      currentRightComponent: Filters,
      active_tab: 0,
      isLoged: false,
      user: {},
      popupMessage: {},
      myAppKey: 0,
      lang: "",
      asking_password:
        SharedStorage.settings.securitySettings.appPassword.turnOn,
      appPassword: SharedStorage.settings.securitySettings.appPassword.password,
      wrong_pass_count: 0
    };
  },
  async beforeCreate() {
    //check loggedin
    if(SharedStorage.currentUser.name){
      this.user = SharedStorage.currentUser;
      this.asking_password =
        SharedStorage.settings.securitySettings.appPassword.turnOn;
      this.appPassword =
        SharedStorage.settings.securitySettings.appPassword.password;
    }else{
      this.$router.push('/login')
    }
    // messageRepository.init();
    this.active_tab = 0;
    EventBus.$on("change_language", lang => {
      // //console.log("change language", lang);
      SharedStorage.language = lang;
      this.lang = lang;
      this.active_tab = 0;
      // this.myAppKey++;
      setCookie("lang", lang, 1000);
    });
    EventBus.$on("showTaskEditor", task => {
      // this.currentRightComponent = null;
      // //console.log("show task editor receive in App.vue");
      if (this.currentRightComponent != TaskEditor) {
        this.currentRightComponent = TaskEditor;
      }
    });
    EventBus.$on("showError", message => {
      // //console.log("show Info", message);

      this.showPopup({ message: message });
    });
    EventBus.$on("profile_changed", () => {
      location.reload();
    });
    EventBus.$on("refresh_app", () => {
      this.reloadPage();
    });
    EventBus.$on("force_logout", async e => {
      if (e) await this.logout();
    });
    EventBus.$on("viewChanged", view => {
      this.currentRightComponent = null;
      switch (view) {
        case "task":
          this.currentRightComponent = TaskEditor;
          break;
        default:
          this.currentRightComponent = Filters;
          break;
      }
    });
    EventBus.$on("extensionChanged", e => {
      // //console.log("event bus change extension", e);
      switch (e) {
        case "filter":
          this.active_tab = 0;
          this.currentRightComponent = Filters;
          break;
        case "settings":
          this.active_tab = 2;
          this.currentRightComponent = Setting;
          break;
        default:
          this.active_tab = 1;
          this.currentRightComponent = Profile;
          break;
      }
    });
  },
  async created() {
    
  },
  beforeMount() {
    if (getCookie("lang") != null) this.lang = getCookie("lang");
    else this.lang = "en";
    SharedStorage.language = this.lang;
  },

  components: {
    LeftExtension,
    RightExtension,
    Filters,
    Setting,
    Profile,
    TaskEditor,
    NavigatorBar,
    Body,
    LoginScreen,
    InfoPopup,
    AuthenticationPopup
  },
  methods: {
    change_extension_tab(e) {
      // //console.log(e);
      if (e.tab_id == "settings") {
        this.currentRightComponent = Setting;
      } else if (e.tab_id == "profile") {
        this.currentRightComponent = Profile;
      } else this.currentRightComponent = Filters;
    },
    async change_user(e) {
      // //console.log("logged success?", e);
      this.user = e;
      this.isLoading = true;
      try {
        SharedStorage.settings = e.setting;
        // //console.log(SharedStorage.settings);
        SharedStorage.currentUser = e;
        // //console.log(SharedStorage.currentUser);
        this.asking_password =
          SharedStorage.settings.securitySettings.appPassword.turnOn;
        this.appPassword =
          SharedStorage.settings.securitySettings.appPassword.password;
        this.isLoged = true;
      } catch (e) {
        // //console.log(e);
        //TODO xử lý lỗi nếu không load được settings
        let message = errorMessages[`${e}`];
        this.showPopup({ message: message });
      } finally {
        this.isLoading = false;
      }
      // this.$refs.body.refreshPosts();
    },
    async logout() {
      await userRepository.logout();
      this.isLoged = false;
    },
    searchPost(e) {
      // //console.log(e);
      this.currentFilter.title = e;
      this.$refs.body.refreshPosts(this.currentFilter);
    },
    showPopup(e) {
      document.getElementById("showInfoModalBtn").click();
      //console.log("show popup", e);
      if (e.message != undefined && e.message != null)
        this.popupMessage = e.message;
      else this.popupMessage = errorMessages[0];
      //console.log(this.popupMessage);
    },
    reloadPage(reload = true) {
      //console.log("reload page");
      if (reload) this.myAppKey++;
    },
    handleIncorrect() {
      this.wrong_pass_count++;
      if (this.wrong_pass_count > 4) {
        this.showPopup({ message: errorMessages[401] });
        this.logout();
        this.wrong_pass_count = 0;
      }
    }
  }
};
</script>

<style>
/* #app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
} */
.extensiontab {
  border-radius: 10px;
  background-color: whitesmoke;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  height: 90vh;
  /* margin-right: 200px; */
  /* padding-right: 50px; */
}
.scroll_extensions {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 99%;
}
.scroll_extensions::-webkit-scrollbar {
  width: 0px;
}

.scroll_extensions::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

.scroll_extensions::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 10px;
}

.scroll_extensions::-webkit-scrollbar-thumb:hover {
  background: red;
}
p {
  margin-bottom: 0 !important;
}
</style>
