<template>
  <div>
    <div class="d-flex justify-content-start">
      <img v-if="post.feeling != ''" class="feeling" :src="feelingArr[parseInt(post.feeling)]" />
      <p class="align-self-center ml-2 eventname">{{post.event_name ==""?translate('no_title'):post.event_name}}</p>
    </div>
    <hr class="divider" />
    <div class="d-flex">
      <div class="d-flex mr-2">
        <v-icon v-text="'mdi-clock'" size="15"></v-icon>
        <p class="mini-text ml-1">{{getTime(post.createTime)}}</p>
      </div>
      <div v-if="post.topic.title" class="d-flex mr-2">
        <v-icon v-text="'mdi-tag'" size="15" color="green"></v-icon>
        <p class="mini-text ml-1">{{translate(post.topic.title)}}</p>
      </div>
      <div v-if="post.freeze" class="d-flex mr-2">
        <v-icon v-text="'mdi-snowflake'" size="15" color="green"></v-icon>
        <p class="mini-text ml-1">{{translate("freezed")}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { translate, TimeConverter } from "../../shared/translator";
import GoodActive from "../../shared/assets/good_active.png";
import HateActive from "../../shared/assets/hate_active.png";
import NaturalActive from "../../shared/assets/natural_active.png";
import ExcellentActive from "../../shared/assets/excellent_active.png";
import DisappointedActive from "../../shared/assets/disappointed_active.png";
const timer = new TimeConverter();
export default {
  props: {
    post: Object
  },
  methods: {
    translate: translate,
    getTime: timer.convertTimeFromNow
  },
  data() {
    return {
      feelingArr: [
        ExcellentActive,
        GoodActive,
        NaturalActive,
        DisappointedActive,
        HateActive
      ]
    };
  }
};
</script>

<style>
.feeling {
  width: 50px;
  height: 50px;
}
.eventname {
  font-weight: bolder;
  word-wrap: break-word;
  user-select: none;
}
.divider {
  margin-top: 0;
  margin-bottom: 0;
}
</style>